<template>
    <v-bottom-sheet
      overlay-opacity="0.7"
      v-model="sheet"
    >
      <v-row>
        <v-col class="pb-16 pl-7">
          <v-row justify="center" align-content="center">
            <v-card elevation="0" width="325" class="text-center">
              <p v-if="todo === 'deleteevent'" class="txtconferm txtreg px-4 py-5">
                Are you sure you want to delete Event?
              </p>

              <p v-if="todo === 'deletepet'" class="txtconferm txtreg px-4 py-5">
                Are you sure you want to delete Pet?
              </p>

              <p v-if="todo === 'deletepost'" class="txtconferm txtreg px-4 py-5">
                Are you sure you want to delete Post?
              </p>

              <!-- <p v-if="todo === 'deletematching'" class="txtconferm txtreg px-4 pt-5">
                Are you sure you want to matching Post?
              </p> -->

              <p v-if="todo === 'deletelostfound'" class="txtconferm txtreg px-4 pt-5">
                Are you sure you want to delete pet lost Post?
              </p>

              <v-divider></v-divider>
              <v-row>
                <v-col cols="5" class="pl-12" @click="cancel">
                  <v-btn text block>Cancel</v-btn>
                </v-col>

                  <v-divider
                  class="ml-8"
                  vertical
                ></v-divider>

                <v-col cols="5" v-if="todo === 'deleteevent'" @click="deleteEvent">
                  <v-btn text block  color="red">Delete</v-btn>
                </v-col>

                <v-col cols="5" v-if="todo === 'deletepet'" @click="deletepet">
                  <v-btn text block  color="red">Delete</v-btn>
                </v-col>

                <v-col cols="5" v-if="todo === 'deletepost'" @click="deletepost">
                  <v-btn text block  color="red">Delete</v-btn>
                </v-col>

                <!-- <v-col cols="5" v-if="todo === 'deletematching'" @click="deletematching">
                  <v-btn text block  color="red">Delete</v-btn>
                </v-col> -->

                <v-col cols="5" v-if="todo === 'deletelostfound'" @click="deleteLost_found">
                  <v-btn text block  color="red">Delete</v-btn>
                </v-col>

              </v-row>
            </v-card>
          </v-row>
        </v-col>
      </v-row>
    </v-bottom-sheet>
</template>

<script>
import axios from 'axios'
export default {
  components: {

  },
  data () {
    return {
      sheet: false,
      info: '',
      todo: ''
    }
  },
  methods: {
    openSheet (info, todo) {
      this.info = info
      this.todo = todo
      this.sheet = true
      console.log('info :', info)
      console.log(this.todo)
    },
    closeDialog () {
      this.sheet = false
    },
    async deleteEvent () {
      const body = {
        reminder_id: this.info.id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'remove_pet_reminder',
        body
      )
      console.log('result :', result)
      this.$emit('Emit', 'closeInfo')
      this.sheet = false
    },
    async deleteLost_found () {
      await this.axios.post(
        process.env.VUE_APP_API + 'remove_pet_fromlost', {
          pet_id: this.info.pet_id
        }
      ).then(res => { console.log('result', res) })
      this.$store.commit('getclosedialog', true)
      this.$store.commit('getupdateMypet', true)
      this.sheet = false
    },
    cancel () {
      this.$emit('Emit', 'cancel')
      this.sheet = false
    },
    async deletepet () {
      const body = {
        user_id: this.info.user_id,
        pet_id: this.info.pet_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'remove_my_pet',
        body
      )
      console.log('result : ', result)
      this.$emit('updateitempet')
      this.$store.commit('getupdateMypet', true)
      this.sheet = false
    },
    async deletepost () {
      const body = {
        post_id: this.info.post_id,
        user_create_id: this.info.user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'remove_post',
        body
      )
      console.log('result', result)
      this.$emit('delete')
      this.$store.commit('getupdateprofile', true)
      this.$store.commit('getupdateTimeline')
      this.$store.commit('getupdatePost', true)
      this.sheet = false
    },
    async deletematching () {
      await this.axios.post(
        process.env.VUE_APP_API + 'remove_pet_match', {
          pet_id: this.info.pet_id
        }
      ).then(res => { console.log('result', res) })
      this.$store.commit('getupdateDeletematching', true)
      this.sheet = false
    }
  }
}
</script>

<style lang="scss" scoped>
.txtconferm{
  font-size: 15px;
}
</style>
