<template>
  <v-container grid-list-xs>
    <v-row>
      <v-col cols="12">
        <v-text-field
          v-model="keysearch"
          outlined
          label="Search"
          append-icon="mdi-magnify"
          @click:append="searchuser"
        ></v-text-field>
      </v-col>

      <v-col v-if="opensearchuser === '1'">
        <v-col
          class="txtbig mt-n9 txtreg"
          v-if="messageerror === 'not found'"
          align="center"
        >
          User not found
        </v-col>
        <v-card
          v-else
          v-for="(item, index) in searchuserDATA"
          :key="index"
          elevation="0"
          class="primary--stoke "
        >
          <v-row>
            <v-col align="center" class="" cols="4" sm="3" md="2">
              <v-avatar size="80">
                <img
                  :src="item.owner_pic"
                />
              </v-avatar>
            </v-col>
            <v-col class="" cols="8" sm="9" md="10">
              <v-row cols="12">
                <h4 class="primary--text">{{ item.first_name_en }} {{ }} {{ item.last_name_en }}</h4>
              </v-row>
              <v-row class="mt-2" cols="12">
                <h6 class="greyB--text" v-if="item.gender === 'M'">
                  {{ 'Male' }} |
                </h6>
                <h6 class="greyB--text" v-else>
                  {{ 'Female' }} |
                </h6>
                <h6 class="greyB--text pl-1">
                  {{ }} {{ item.mobile_no }}
                </h6>
              </v-row>
              <v-row class="mt-2 ml-1">
                <v-btn
                  @click="openFriendListdialog(item)"
                  rounded
                  height="30"
                  elevation="0"
                  color="Gray"
                  class=""
                >
                  <v-icon>mdi-account-outline</v-icon>
                  <h5>View Profile</h5></v-btn
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
    <FriendListdialog ref="FriendListdialog" />
  </v-container>
</template>

<script>
import axios from 'axios'
import FriendListdialog from '@/components/Timeline/FriendListdialog'
export default {
  components: {
    FriendListdialog
  },
  data () {
    return {
      searchuserDATA: [],
      keysearch: '',
      opensearchuser: '',
      messageerror: null
    }
  },
  methods: {
    async searchuser () {
      this.opensearchuser = '1'
      console.log('Test Username')
      const body = {
        key_search: this.keysearch,
        key_type: 'username'
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'search_user',
        body
      )
      this.searchuserDATA = result.data.user_data
      this.messageerror = result.data.message
      console.log(this.messageerror)
      // console.log('-hv,^]', this.searchuserDATA)
    },
    openFriendListdialog (info) {
      this.$refs.FriendListdialog.openDialog(info)
    }
  }
}
</script>

<style></style>
