<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card color="greyF1">
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="(dialog = false), closeDialog()">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Lost&Found</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <Petprofile v-bind:info="info" v-bind:showmenu="'true'" v-bind:menu_type="'lost'"/>

        <v-card class="mt-1 px-0 pt-2" elevation="0">
          <v-row justify="center" align-content="center" class="mx-0">
            <v-col cols="12">
              <v-form ref="form" v-model="valid" lazy-validation class="">
                <div class="txtbold secondary--text pb-3">
                  DETAIL
                </div>
                <v-textarea
                  dense
                  auto-grow
                  outlined
                  rows="1"
                  row-height="15"
                  label="Detail"
                  v-model="data.title"
                  clearable
                ></v-textarea>

                <v-menu
                  v-model="menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-textarea
                      dense
                      auto-grow
                      outlined
                      rows="1"
                      row-height="15"
                      label="Last seen"
                      v-model="data.datetime"
                      clearable
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      readonly
                      :rules="requireRule"
                    ></v-textarea>
                  </template>
                  <v-date-picker
                    v-model="data.datetime"
                    @input="menu = false"
                  ></v-date-picker>
                </v-menu>

                <v-textarea
                  dense
                  auto-grow
                  outlined
                  rows="1"
                  row-height="15"
                  label="Contact"
                  v-model="data.contact"
                  clearable
                  append-icon="mdi-phone"
                  :rules="requireRule"
                ></v-textarea>

                <div class="txtbold secondary--text pb-3">
                  LAST SEEN LOCATION
                </div>

                <getCoordinate ref="getCoordinate" />

                <v-textarea
                  class="pt-5"
                  dense
                  auto-grow
                  outlined
                  rows="1"
                  row-height="15"
                  label="Location"
                  v-model="data.lastseenplace"
                  clearable
                  append-icon="mdi-map-marker"
                  :rules="requireRule"
                ></v-textarea>

                <v-btn
                  elevation="0"
                  block
                  class="primary white--text mt-10"
                  :disabled="!valid"
                  color="primary"
                  v-if="this.type === 'Create'"
                  @click="createLost()"
                >
                  Save
                </v-btn>
                <v-btn
                  elevation="0"
                  block
                  class="primary white--text mt-10"
                  :disabled="!valid"
                  color="primary"
                  v-if="this.type === 'Edit'"
                  @click="edit()"
                >
                  Save
                </v-btn>
              </v-form>
            </v-col>
          </v-row>
        </v-card>

        <!-- <v-list three-line subheader>
          <v-container class=" mt-n3">
            <v-row cols="12">
              <v-col cols="12">
                <div>
                  <v-select
                    v-if="type === 'Create'"
                    :items="petName"
                    item-text="name"
                    return-object
                    label="Select your pet"
                    dense
                    outlined
                    v-model="petId"
                  >
                  </v-select>

                  <v-card outlined elevation="0" class="greyC--stoke">
                    <v-row v-if="type === 'Create'">
                      <v-img
                        v-if="petId === ''"
                        class="ml-3 greyC"
                        height="112"
                        width="112"
                        src="../../assets/icon/icon_PETDY.png"
                      ></v-img>
                      <v-img
                        v-else
                        class="ml-3 greyC"
                        height="112"
                        width="112"
                        :src="petId.pic"
                      ></v-img>
                      <v-col cols="7" class="">
                        <v-row>
                          <v-col cols="12" class="txtbold">
                            {{ petId.name }}
                          </v-col>
                          <v-col v-if="petId !== null && petId !== ''">
                            <h6>
                              {{ petId.pettype }} | {{ petId.gender }} |
                              {{ petId.furcolor }}
                            </h6>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>

                    <v-row v-if="this.type === 'Edit'">
                      <v-img
                        v-if="info.pic !== ''"
                        class="ml-3 greyC"
                        height="112"
                        width="112"
                        :src="info.pic"
                      ></v-img>
                      <v-img
                        v-if="info.pic === ''"
                        class="ml-3 greyC"
                        height="112"
                        width="112"
                        src="../../assets/icon/icon_PETDY.png"
                      ></v-img>
                      <v-col cols="7" class="">
                        <v-row>
                          <v-col cols="12" class="txtbold">
                            {{ info.name }}
                          </v-col>
                          <v-col>
                            <h6>
                              {{ info.pettype }} | {{ info.gender }} |
                              {{ info.furcolor }}
                            </h6>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-card>
                </div>
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-card-text class="mt-5">
                    <h3>Detail</h3>
                  </v-card-text>
                  <v-text-field
                    class=""
                    v-model="data.title"
                    :rules="DetailRules"
                    outlined
                    label="Detail"
                    clearable
                    required
                  ></v-text-field>
                  <v-card-text>
                    <h3>Last seen</h3>
                  </v-card-text>
                  <v-menu
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="data.datetime"
                        :rules="datetimeRules"
                        label="Last seen"
                        readonly
                        outlined
                        v-bind="attrs"
                        v-on="on"
                        append-icon="mdi-calendar"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="data.datetime"
                      @input="menu = false"
                    ></v-date-picker>
                  </v-menu>
                  <v-text-field
                    v-model="data.lastseenplace"
                    :rules="LocationRules"
                    outlined
                    label="Location"
                    append-icon="mdi-map-marker"
                  ></v-text-field>
                </v-form>
              </v-col>
            </v-row>
          </v-container>
          <getCoordinate ref="getCoordinate" />

          <v-col>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Create'"
              @click="addpet()"
            >
              Save
            </v-btn>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Edit'"
              @click="edit()"
            >
              Save
            </v-btn>
          </v-col>
        </v-list> -->
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from 'axios'
import getCoordinate from '@/components/getCoordinate'
import Petprofile from '@/components/Petprofile'
export default {
  components: {
    getCoordinate,
    Petprofile
  },
  data () {
    return {
      lostdetail: null,
      currentPositions: { lat: 0, lng: 0 },
      lastseen_position: { lat: 0, lng: 0 },
      menu: false,
      delete_menu: false,

      valid: true,
      title: '',
      DetailRules: [v => !!v || 'Detail is required'],
      datetime: '',
      datetimeRules: [v => !!v || 'Last seen is required'],
      contact: '',
      MobileRules: [v => !!v || 'Mobile Number is required'],
      Location: '',
      LocationRules: [v => !!v || 'Location is required'],
      requireRule: [(v) => !!v || 'This field is required'],

      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      type: null,
      info: '',

      data: {
        lastseen_longitude: '',
        lastseen_latitude: '',
        title: '',
        lastseenplace: '',
        datetime: '',
        contact: '',
        pet_id: ''
      },
      petName: [],
      useridData: [],
      petId: '',
      userID: ''
    }
  },
  computed: {
    closedialog () {
      return this.$store.state.closedialog
    }
  },
  watch: {
    closedialog (newValue, oldValue) {
      if (newValue === true) {
        this.dialog = false
        this.$store.commit('getclosedialog', false)
      }
    },
    dialog (neValue) {
      if (neValue === true) {
        this.$refs.getCoordinate.clearMarker()
      }
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    setInitmarker () {
      this.$refs.getCoordinate.setInitMarker(this.lastseen_position.lat, this.lastseen_position.lng)
    },
    async getpet () {
      const body = {
        user_id: this.userID
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_by_userid',
        body
      )
      console.log('result', result)
      this.useridData = result.data.pet_data
    },
    async createLost () {
      if (this.$refs.form.validate() === true) {
        this.data.pet_id = this.info.pet_id
        this.data.lastseen_longitude = this.$store.state.Locationlng
        this.data.lastseen_latitude = this.$store.state.Locationlat
        console.log(this.data)
        await this.axios
          .post(process.env.VUE_APP_API + 'set_pet_fromlost', this.data)
          .then(res => {
            console.log('result', res)
          })
        this.dialog = false
      } else {
        console.log('can not create Lost&Found ')
      }
      this.$store.commit('getupdateLostFound')
      this.$store.commit('getupdateMypet', true)
    },
    async edit () {
      if (this.$refs.form.validate() === true) {
        this.data.lastseen_longitude =
          this.$store.state.Locationlng || this.info.lastseen_longitude
        this.data.lastseen_latitude =
          this.$store.state.Locationlat || this.info.lastseen_latitude
        await this.axios
          .post(process.env.VUE_APP_API + 'set_pet_fromlost', this.data)
          .then(res => {
            console.log('result', res)
          })
        this.dialog = false
      } else {
        console.log('can not create Lost&Found')
      }
      this.$store.commit('getupdateLostFound')
      this.$store.commit('getupdateMypet', true)
    },

    async openDialog (info, type) {
      console.log(info)
      this.$store.commit('getloading', true)
      this.info = info
      this.type = type
      if (info.status === 1) {
        this.type = 'Create'
      } else if (info.status === 0) {
        this.type = 'Edit'
      }
      this.dialog = true
      // this.$refs.getCoordinate.getcurrentPosition()
      await this.getpetlost()
      this.userID = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      await this.getpet()

      for (let index = 0; index < this.useridData.length; index++) {
        const element = this.useridData[index]
        this.petName.push(element)
      }
      console.log(this.type)
      if (this.type === 'Edit') {
        // this.$store.commit('getshowImgstatusTRUE')
        this.data.lastseen_longitude = this.lostdetail.lastseen_longitude
        this.data.lastseen_latitude = this.lostdetail.lastseen_latitude
        this.data.title = this.lostdetail.title
        this.data.lastseenplace = this.lostdetail.lastseenplace
        this.data.datetime = this.lostdetail.datetime.split('T')[0]
        this.data.contact = this.lostdetail.contact
        this.data.pet_id = this.lostdetail.pet_id
        this.setInitmarker()
      } else if (this.type === 'Create') {
        this.$refs.form.reset()
        // this.$store.commit('getshowImgstatusFALSE')
        this.petId = ''
        this.data.lastseenplace = null
        this.data.datetime = null
        this.data.contact = null
        this.$store.commit('getshowImgstatusTRUE')
      }
    },
    async getpetlost () {
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_fromlost'
      )
      console.log('result', result)
      await result.data.list_pet_fromlost.forEach(lost => {
        if (lost.pet_id === this.info.pet_id) {
          this.lostdetail = lost
          this.lastseen_position.lat = lost.lastseen_latitude
          this.lastseen_position.lng = lost.lastseen_longitude
        }
      })
    }
  }
}
</script>
