<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary" elevation="0">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Donate Blood</v-toolbar-title>
        </v-toolbar>
        <v-list three-line subheader>
          <v-container class=" mt-n3">
            <v-row>
              <v-col align="center" cols="12">
                <v-card elevation="0">
                  <v-img
                    width="200"
                    height="200"
                    v-if="info.picture !== ''"
                    :src="info.picture"
                  ></v-img>
                  <v-img
                    width="200"
                    height="200"
                    v-if="info.picture === ''"
                    src="../../assets/icon/icon_PETDY.png"
                  ></v-img>
                </v-card>
              </v-col>
              <v-col align="center" cols="12"
                ><span class=" secondary--text txtbold txtbig">{{
                  info.title
                }}</span>
              </v-col>
              <v-col align="end" cols="12"
                ><v-icon small class=" greyB--text txtsmall collocateIcon "
                  >mdi-clock-outline</v-icon
                >
                <span class="ml-1  greyB--text txtsmall"
                  >{{ info.create_date }} </span
                >
              </v-col>
              <v-card height="100%" elevation="0">
                <v-col cols="12" class="mb-5"><span v-html="info.detail"></span> </v-col>
              </v-card>

              <v-col class="bottomBTN">
                <v-btn block class="brown white--text" elevation="0" @click="openchat">
                  Donate Blood
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-list>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      pathS3: process.env.VUE_APP_S3,
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: ''
    }
  },
  methods: {
    async openchat () {
      const body = {
        one_id: this.info.one_id
      }
      const headers = {
        Authorization:
        'Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1'
      }
      const result = await axios.post(
        'https://chat-api.one.th/manage/get_link_addfriend',
        body,
        { headers }
      )
      console.log('result', result)
      window.open(result.data.link)
    },
    openDialog (info) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
    },
    closeDialog () {
      this.dialog = false
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  }
}
</script>

<style>
.bottomBTN {
  position: absolute;
  bottom: 0;
}
</style>
