<template>
  <v-tabs center-active centered color="secondary" height="40px" v-model="tab">
    <v-tab v-for="(menu, i) in itemsTab" :key="i" @click="goPage(i)">
      <span class="txtsmall txtbold">{{menu.text}}</span>
    </v-tab>
  </v-tabs>
</template>

<script>
export default {
  props: ['active'],
  created () {
    this.tab = this.goPage(0)
  },
  data () {
    return {
      tab: 0,
      itemsTab: [
        { id: 0, text: 'Username' },
        { id: 1, text: 'One Email' },
        { id: 2, text: 'My QR Code' }

      ]
    }
  },
  methods: {
    goPage (i) {
      this.$emit('select', i)
    }
  }
}
</script>

<style scoped>
>>> .v-slide-group__prev {
  display: none !important;
}
</style>
