<template>
  <v-dialog
    v-model="dialog"
    hide-overlay
    transition="dialog-bottom-transition"
    persistent
  >
    <v-card class="my-0 py-0">
      <v-toolbar dark color="primary" elevation="0">
        <v-btn icon dark @click="dialog = false">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-toolbar-title>Edit User Profile</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <UploadeImage
        ref="addpetDialog"
        v-bind:width="250"
        v-bind:height="250"
        v-bind:classupload="true"
      ></UploadeImage>

      <v-row class="mx-0">
        <v-col>
          <v-row class="mx-1">
            <v-textarea
              :rules="requireRule"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="nickname"
              label="Display Name"
            ></v-textarea>
          </v-row>

          <v-row class="mx-1">
            <v-textarea
              :rules="requireRule"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="mobile"
              label="Mobile No"
            ></v-textarea>
          </v-row>
          <v-row class="mx-1">
            <v-textarea
              :rules="requireRule"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="birthdate"
              label="Birth Date"
            ></v-textarea>
          </v-row>
          <v-row class="mx-1">
            <v-textarea
              :rules="requireRule"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="email"
              label="Email"
            ></v-textarea>
          </v-row>

          <v-row class="mx-1">
            <v-autocomplete
              :rules="requireRule"
              v-model="country"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              label="Country"
              :items="countryList"
              item-text="name_en"
              @change="selectCountry(country)"
            ></v-autocomplete>

            <v-textarea
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="address"
              label="Address"
            ></v-textarea>

            <v-autocomplete
            v-if="country === 'Thailand'"
              v-model="province"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              label="Province"
              :items="provinceList"
              item-text="name_in_english"
              @change="selectProvince(province)"
            ></v-autocomplete>

            <v-textarea
            v-if="country !== 'Thailand'"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="province"
              label="Province"
            ></v-textarea>

            <v-autocomplete
            v-if="country === 'Thailand'"
              v-model="district"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              label="District"
              :items="districtList"
              item-text="name_in_english"
              @change="selectDistrict(district)"
            ></v-autocomplete>

            <v-textarea
            v-if="country !== 'Thailand'"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="district"
              label="District"
            ></v-textarea>

            <v-autocomplete
            v-if="country === 'Thailand'"
              v-model="subdistrict"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              label="Subdistrict"
              :items="subdistrictList"
              item-text="name_in_english"
              @change="selectSubdistrict(subdistrict)"
            ></v-autocomplete>

            <v-textarea
            v-if="country !== 'Thailand'"
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="subdistrict"
              label="Subdistrict"
            ></v-textarea>

            <v-textarea
              auto-grow
              outlined
              rows="1"
              row-height="15"
              dense
              v-model="zipCode"
              label="Zip code"
            ></v-textarea>
          </v-row>
        </v-col>
      </v-row>
      <v-row class="mx-0 mt-16">
        <v-col cols="12" class="px-0">
          <v-divider></v-divider>
          <v-row justify="end" class="mt-0 mx-0">
            <v-btn text @click="dialog = false">CLOSE</v-btn>
            <v-btn text @click="editprofile">SAVE</v-btn>
          </v-row>
        </v-col>
      </v-row>
    </v-card>

    <loading></loading>
  </v-dialog>
</template>

<script>
import UploadeImage from '@/components/uploadeImage'
import axios from 'axios'
import loading from '@/components/Loading'
export default {
  components: {
    UploadeImage,
    loading
  },
  data () {
    return {
      dialog: false,
      info: '',
      nickname: null,
      email: null,
      mobile: null,
      birthdate: '',
      address: '',
      province: '',
      district: '',
      subdistrict: '',
      zipCode: '',
      country: '',
      countryList: [],
      provinceList: [],
      districtList: [],
      subdistrictList: [],

      countryID: '',
      provinceID: '',
      districtID: '',
      subdistrictID: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async editprofile () {
      this.$store.commit('getloading', true)
      var formData = new FormData()
      if (this.$store.state.imgBlob === null) {
        formData.append('slug', this.nickname)
        formData.append('user_id', this.info.user_id)
        formData.append('oneemail', this.info.oneemail)
        formData.append('first_name_th', this.info.first_name_th)
        formData.append('last_name_th', this.info.last_name_th)
        formData.append('mobile_no', this.mobile)
        formData.append('gender', this.info.gender)
        formData.append('address', this.address)
        formData.append('country_id', this.countryID)
        formData.append('province_id', this.provinceID)
        formData.append('districts_id', this.districtID)
        formData.append('subdistrict_id', this.subdistrictID)
        formData.append('zipcode', this.zipCode)
        formData.append('email', this.email)
        formData.append('birthdate', this.birthdate)
        formData.append('pic', '')
        formData.append('country', this.country)
        formData.append('province', this.province)
        formData.append('district', this.district)
        formData.append('subdistrict', this.subdistrict)
      } else {
        formData.append('slug', this.nickname)
        formData.append('user_id', this.info.user_id)
        formData.append('oneemail', this.info.oneemail)
        formData.append('first_name_th', this.info.first_name_th)
        formData.append('last_name_th', this.info.last_name_th)
        formData.append('mobile_no', this.mobile)
        formData.append('gender', this.info.gender)
        formData.append('address', this.address)
        formData.append('country_id', this.countryID)
        formData.append('province_id', this.provinceID)
        formData.append('districts_id', this.districtID)
        formData.append('subdistrict_id', this.subdistrictID)
        formData.append('zipcode', this.zipCode)
        formData.append('email', this.email)
        formData.append('birthdate', this.birthdate)
        formData.append('pic', this.$store.state.imgBlob, 'png')
        formData.append('country', this.country)
        formData.append('province', this.province)
        formData.append('district', this.district)
        formData.append('subdistrict', this.subdistrict)
      }

      const result = await axios.post(
        process.env.VUE_APP_API + 'edit_Profile',
        formData
      )
      console.log('result', result)
      this.$store.commit('getupdatUserprofile', true)
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
      this.$nextTick(function () {
        if (this.$store.state.loading === true) {
          this.$store.commit('getloading', false)
        }
      })
      this.dialog = false
    },
    async openDialog (info) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
      this.nickname = this.info.nickname
      this.email = this.info.email
      this.mobile = this.info.mobile_no
      this.birthdate = this.info.birthdate.split('T')[0]
      this.address = this.info.address
      this.province = this.info.province
      this.district = this.info.district
      this.subdistrict = this.info.subdistrict
      this.zipCode = this.info.zipcode
      this.country = this.info.country
      this.countryID = this.info.country_id
      this.provinceID = this.info.province_id
      this.districtID = this.info.districts_id
      this.subdistrictID = this.info.subdistrict_id

      await this.getcountry()
      await this.getprovince()
      await this.getdistrict(this.info.province_id)
      await this.getsubdistrict(this.info.districts_id)
    },
    selectCountry (data) {
      this.countryList.forEach(focus => {
        if (data === focus.name_en) {
          this.countryID = focus.id
        }
      })
    },
    async selectProvince (data) {
      this.provinceList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.provinceID = focus.id
        }
      })
      await this.getdistrict(this.provinceID)
    },
    async selectDistrict (data) {
      this.districtList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.districtID = focus.id
        }
      })
      await this.getsubdistrict(this.districtID)
    },
    selectSubdistrict (data) {
      this.subdistrictList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.subdistrictID = focus.id
        }
      })
    },
    async getcountry () {
      this.countryList = []
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_countries'
      )
      this.countryList = result.data.countries_data
    },
    async getprovince () {
      this.provinceList = []
      const result = await axios.post(process.env.VUE_APP_API + 'get_provices')
      this.provinceList = result.data.provices_data
    },
    async getdistrict (id) {
      this.districtList = []
      const body = {
        province_id: id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_districts',
        body
      )
      console.log('result', result)
      this.districtList = result.data.districts_data
    },
    async getsubdistrict (id) {
      this.subdistrictList = []
      const body = {
        district_id: id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_subdistricts',
        body
      )
      this.subdistrictList = result.data.subdistricts_data
    }
  }
}
</script>

<style lang="scss" scoped></style>
