<template>
  <div>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card color="greyF1" v-if="info.hardware_status === null">
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Device</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <Petprofile v-bind:info="info" v-bind:showmenu="'false'" />

        <v-card
          tile
          elevation="0"
          class="mt-1 pt-16"
          v-if="whenshow === 'beforeScan'"
          height="660"
        >
          <v-row justify="center" class="mt-16">
            <v-col>
              <v-row justify="center">
                <v-card height="150" width="150" elevation="0">
                  <v-img src="../../../assets/icon/Device/deviceScan.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center">
            <v-col cols="6">
              <v-row justify="center">
                Scan QR Code
              </v-row>
              <v-row justify="center">
                to connect the device
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center">
            <v-col cols="10">
              <v-row justify="center">
                <v-btn elevation="0" block color="primary" @click="openscanner"
                  >Scan Qr Code</v-btn
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>

        <v-card v-if="whenshow === 'Scan'">
          <QRScaner
            v-on:checkpair="checkpair"
            v-on:device_minor="get_minor"
            v-on:QRcode="getQRscan"
            v-on:close="whenshow = 'beforeScan'"
            v-bind:action="'connect'"
            v-bind:ScanAction="'connectpet'"
          />
        </v-card>

        <v-card
          tile
          elevation="0"
          class="mt-1 text-center"
          v-if="whenshow === 'afterScan'"
          height="660"
        >
          <v-row justify="center">
            <v-col>
              <v-row justify="center" class="mt-16">
                <v-card height="300" width="300" elevation="0">
                  <v-img src="../../../assets/icon/Device/pet-shop.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center">
            <v-col cols="10">
              <v-row justify="center" class="mt-10">
                <v-btn
                  elevation="0"
                  block
                  color="primary"
                  class="white--text"
                  @click="connectDevice"
                  v-if="this.info.hardware_status === null"
                  >Connect collar</v-btn
                >
              </v-row>
              <v-row justify="center">
                <v-btn
                  elevation="0"
                  block
                  color="shop"
                  class="white--text"
                  v-if="this.info.hardware_status === 'activate_pair'"
                  >unpair collar</v-btn
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-card>

      <v-card color="greyF1" v-if="info.hardware_status === 'activate_pair'">
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Device</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <Petprofile v-bind:info="info" v-bind:showmenu="'false'" />

        <v-card
          tile
          elevation="0"
          class="mt-1"
          height="660"
          v-if="whenshow === 'beforeScan'"
        >
          <v-row justify="center">
            <v-col>
              <v-row justify="center" class="mt-16">
                <v-card height="300" width="300" elevation="0">
                  <v-img src="../../../assets/icon/Device/pet-shop.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center">
            <v-col cols="10">
              <v-row justify="center" class="mt-10">
                <v-btn
                  elevation="0"
                  block
                  color="primary"
                  class="white--text"
                  @click="connectDevice"
                  v-if="this.info.hardware_status === null"
                  >Connect collar</v-btn
                >
              </v-row>
              <v-row justify="center">
                <v-btn
                  elevation="0"
                  block
                  color="shop"
                  class="white--text "
                  v-if="this.info.hardware_status === 'activate_pair'"
                  @click="openscanner"
                  >unpair collar</v-btn
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>

        <v-card v-if="whenshow === 'Scan'">
          <QRScaner
            v-on:checkpair="checkpair"
            v-on:unpair="openDialogUnpair"
            v-bind:disconnect="true"
            v-bind:action="'disconnect'"
            v-on:close="whenshow = 'beforeScan'"
            v-on:QRcode="getQRscan"
            v-bind:ScanAction="'connectpet'"
          />
        </v-card>

        <v-card
          tile
          elevation="0"
          class="mt-1"
          height="660"
          v-if="whenshow === 'afterScan'"
        >
          <v-row justify="center">
            <v-col>
              <v-row justify="center" class="mt-16">
                <v-card
                  height="150"
                  width="150"
                  elevation="0"
                  v-if="pair_status === false"
                >
                  <v-img src="../../../assets/icon/Device/deviceScan.png">
                  </v-img>
                </v-card>

                <v-card
                  height="300"
                  width="300"
                  elevation="0"
                  v-if="pair_status === true"
                >
                  <v-img src="../../../assets/icon/Device/pet-shop.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center" v-if="pair_status === false">
            <v-col cols="6">
              <v-row justify="center">
                Scan QR Code
              </v-row>
              <v-row justify="center">
                to connect the device
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center" v-if="pair_status === false">
            <v-col cols="10">
              <v-row justify="center">
                <v-btn elevation="0" block color="primary" @click="openscanner"
                  >Scan Qr Code</v-btn
                >
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center" v-if="pair_status === true">
            <v-col cols="10">
              <v-row justify="center">
                <v-btn
                  elevation="0"
                  block
                  color="shop"
                  class="white--text "
                  @click="openscanner"
                  >unpair collar</v-btn
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialogUnpair"
      transition="dialog-bottom-transition"
      persistent
    >
      <v-card color="white" class="text-center">
        <v-toolbar dark color="primary" elevation="0">
          <v-toolbar-title>Confirm</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-icon @click="dialogUnpair = false" class="ml-5">mdi-close</v-icon>
        </v-toolbar>
        <v-row class="px-5 py-3">
          <v-col class="px-5" cols="12">
            <v-row>
              please fill your birth date to confirm unpair the collar
            </v-row>
            <v-row class="mt-5">
              <v-textarea
                dense
                auto-grow
                outlined
                rows="1"
                row-height="15"
                v-model="birthdate"
                label="YYYY-MM-DD"
              ></v-textarea>
            </v-row>

            <v-row class="">
              <v-btn
                elevation="0"
                color="shop"
                block
                class="white--text"
                @click="unpairDevice(birthdate)"
                >unpair device</v-btn
              >
            </v-row>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogStatus" transition="dialog-bottom-transition">
      <v-card color="white" class="text-center" v-if="action === 'connect'">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Connect Complete
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="primary">mdi-check-circle-outline</v-icon> -->
            <sweetalert-icon icon="success"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>

      <v-card color="white" class="text-center" v-if="action === 'unpair'">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Unpair Complete
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="primary">mdi-check-circle-outline</v-icon> -->
            <sweetalert-icon icon="success"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>

      <v-card color="white" class="text-center" v-if="action === 'connectERR'">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Can not connect device
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="shop">mdi-close-circle-outline</v-icon> -->
            <sweetalert-icon icon="error"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>

      <v-card color="white" class="text-center" v-if="action === 'unpairERR'">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Can not unpair device
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="shop">mdi-close-circle-outline</v-icon> -->
            <sweetalert-icon icon="error"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>

      <v-card color="white" class="text-center" v-if="action === 'birtdateERR'">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Can not unpair device
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="shop">mdi-close-circle-outline</v-icon> -->
            <sweetalert-icon icon="error"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Petprofile from '@/components/Petprofile'
import QRScaner from '@/components/QRScaner'
import axios from 'axios'
import SweetalertIcon from 'vue-sweetalert-icons'
export default {
  components: {
    Petprofile,
    QRScaner,
    SweetalertIcon
  },
  data () {
    return {
      Qrdecode: '',
      secretKey: 'petdy-secret_key',
      value: 'HELLO WORLD',
      size: 300,
      dialog: false,
      info: '',
      whenshow: 'beforeScan',
      resultScan: '',
      dialogUnpair: false,
      birthdate: '',
      pair_status: true,
      dialogStatus: false,
      action: '',
      currentPositions: [],
      device_minor: null
    }
  },
  methods: {
    get_minor (minor) {
      this.device_minor = minor
    },
    checkpair (HWserial) {
      if (this.info.hardware_sn !== HWserial) {
        this.$store.commit('getPair_status', false)
      } else {
        this.$store.commit('getPair_status', true)
      }
    },
    openDialogUnpair (val) {
      if (val === true) {
        this.birthdate = ''
        this.dialogUnpair = true
      }
    },
    async unpairDevice (birthdate) {
      if (
        birthdate ===
        JSON.parse(this.$ls.get('userprofile'))[0].birthdate.split('T')[0]
      ) {
        this.dialogUnpair = false

        var body = {
          hw_token: this.resultScan,
          account_owner_id: JSON.parse(this.$ls.get('userprofile'))[0].user_id,
          pet_id: this.info.pet_id
        }
        const result = await axios.post(
          process.env.VUE_APP_API + 'disconnect_device',
          body
        )
        // this.whenshow = 'beforeScan'
        this.dialogUnpair = false

        this.$store.commit('getupdateMypet', true)
        if (result.data.status === 'success') {
          this.openStatusDialog('unpair')
        } else {
          this.openStatusDialog('unpairERR')
        }
        this.$store.commit('getupdatepetLocation', true)
      } else {
        this.dialogUnpair = false
        this.openStatusDialog('birtdateERR')
      }
      this.whenshow = 'beforeScan'
      this.pair_status = false
      this.$nextTick(() => {
        this.getpetdetail()
      })
    },
    openDialog (info) {
      console.log(info)
      this.$store.commit('getloading', true)
      this.info = info
      this.dialog = true
      this.getcurrentPosition()
    },
    openscanner () {
      this.whenshow = 'Scan'
    },
    getQRscan (event) {
      this.resultScan = event
      this.whenshow = 'afterScan'
    },
    async connectDevice () {
      var body = {
        hw_token: this.resultScan,
        account_owner_id: JSON.parse(this.$ls.get('userprofile'))[0].user_id,
        pet_id: this.info.pet_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'connect_device',
        body
      )
      console.log('result : ', result)
      this.$nextTick(() => {
        this.getpetdetail()
      })
      this.pair_status = true
      this.$store.commit('getupdateMypet', true)
      if (result.data.status === 'success') {
        await this.iBeacon_updateLocation(this.device_minor)
        this.$store.commit('getupdatepetLocation', true)
        this.openStatusDialog('connect')
      } else {
        this.openStatusDialog('connectERR')
      }
    },
    async getcurrentPosition () {
      const position = await this.getcurrentPosition2()
      this.currentPositions.push({
        lat: position.coords.latitude,
        lng: position.coords.longitude
      })
    },
    async getcurrentPosition2 () {
      return new Promise(function (resolve, reject) {
        navigator.geolocation.getCurrentPosition(resolve, reject)
      })
    },
    async iBeacon_updateLocation (minor) {
      var date = new Date()
      date.setHours(date.getHours() + 7)
      var isodate = date.toISOString()
      const body = {
        // longitude: this.currentPositions[0].lng,
        // latitude: this.currentPositions[0].lat,
        // pet_id: this.info.pet_id,
        event_stage: '',
        major: '',
        minor: minor,
        platform: '',
        rssi: '',
        timestamp:
          isodate.split('T')[0] + ' ' + isodate.split('T')[1].split('.')[0],
        user_latitude: this.currentPositions[0].lat,
        user_longitude: this.currentPositions[0].lng,
        uuid: '',
        one_id: JSON.parse(this.$ls.get('oneprofile')).one_id
      }
      console.log(body)
      const result = await axios.post(
        process.env.VUE_APP_API + 'beacon_update_location',
        body
      )
      console.log('result : ', result)
    },
    async openStatusDialog (val) {
      this.action = val
      this.dialogStatus = true
      setTimeout(() => {
        this.dialogStatus = false
        this.pair_status = true
      }, 1500)
    },
    async getpetdetail () {
      const body = {
        user_id: JSON.parse(this.$ls.get('userprofile'))[0].user_id
      }

      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_by_userid',
        body
      )
      console.log('result : ', result)
      result.data.pet_data.forEach(pet => {
        if (pet.pet_id === this.info.pet_id) {
          this.info = pet
        }
      })
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  watch: {
    dialog (newValue, oldValue) {
      if (oldValue === true && newValue === false) {
        this.whenshow = 'beforeScan'
      }
    }
  }
}
</script>

<style lang="scss" scoped></style>
