<template>
  <div>
    <TabsMenu :active="tab" @select="selectTab"/>

    <Blog v-if="tab === 0"/>
    <BlogBreed v-if="tab === 1"/>
    <LostFound v-if="tab === 2"/>
    <FindHome v-if="tab === 3"/>
    <!-- <Matching v-if="tab === 3"/> -->
    <Donate v-if="tab === 4"/>

  </div>
</template>

<script>
import Blog from '@/components_content/Community/Blog'
import BlogBreed from '@/components_content/Community/BlogBreed'
import LostFound from '@/components_content/Community/LostFound'
import FindHome from '@/components_content/Community/FindHome'
// import Matching from '@/components/Community/Matching'
import Donate from '@/components_content/Community/Donate'
import TabsMenu from '@/components_content/Community/TabsMenu'
export default {
  name: 'Communitydesktop',
  components: {
    Blog,
    LostFound,
    FindHome,
    BlogBreed,
    Donate,
    TabsMenu

  },
  created () {
    this.tab = parseInt(this.$route.params.tab)
  },
  data () {
    return {
      tab: 0
    }
  },
  methods: {
    selectTab (v) {
      this.tab = v
    }
  }
}
</script>
