<template>
  <div>
    <GmapMap
      ref="mapforgetcoordinate"
      id="mapforgetcoordinate"
      :center="{ lat: currentLat, lng: currentLng }"
      :zoom="15"
      map-type-id="roadmap"
      :options="{
        minZoom: 8,
        zoomControl: false,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: false,
        disableDefaultUI: true,
        clickableIcons: false
      }"
      style="width: 100%; height: 700px"
    >
      <GmapCustomMarker
        v-for="(m, index) in markers"
        :key="index"
        :marker="m.position"
        :animation="1"
      >
        <v-img
          v-if="selectedindex === index && selectedMarkerStatus === true"
          width="40"
          height="40"
          src="../../assets/icon/PetMarker/pin.png"
          @click="openMarkerInfoDialog(m, 'petmap', index)"
        >
        </v-img>

        <v-img
          v-else-if="m.status === 1 && m.realuser_id !== userID"
          @click="openMarkerInfoDialog(m, 'petmap', index)"
          src="../../assets/icon/PetMarker/NormalPet.png"
        >
          <v-avatar
            size="27.75"
            color="primary"
            rounded="circle"
            class="markerAvatar"
          >
            <v-img :src="m.pic"> </v-img>
          </v-avatar>
        </v-img>

        <v-img
          v-else-if="m.status === 0 && m.realuser_id !== userID"
          @click="openMarkerInfoDialog(m, 'petmap', index)"
          src="../../assets/icon/PetMarker/LostPet.png"
        >
          <v-avatar
            size="27.75"
            color="primary"
            rounded="circle"
            class="markerAvatar"
          >
            <v-img :src="m.pic"> </v-img>
          </v-avatar>
        </v-img>

        <v-img
          v-else-if="m.status === 1 && m.realuser_id === userID"
          @click="openMarkerInfoDialog(m, 'petmap', index)"
          src="../../assets/icon/PetMarker/MyPet.png"
        >
          <v-avatar
            size="27.75"
            color="primary"
            rounded="circle"
            class="markerAvatar"
          >
            <v-img :src="m.pic"> </v-img>
          </v-avatar>
        </v-img>

        <v-img
          v-else-if="m.status === 0 && m.realuser_id === userID"
          @click="openMarkerInfoDialog(m, 'petmap', index)"
          src="../../assets/icon/PetMarker/LostPet.png"
        >
          <v-avatar
            size="27.75"
            color="primary"
            rounded="circle"
            class="markerAvatar"
          >
            <v-img :src="m.pic"> </v-img>
          </v-avatar>
        </v-img>
      </GmapCustomMarker>
    </GmapMap>
    <MarkerInfo ref="MarkerInfo"></MarkerInfo>
  </div>
</template>

<script>
import axios from 'axios'
import GmapCustomMarker from 'vue2-gmap-custom-marker'
import MarkerInfo from '@/components/Home/MarkerInfo'
// import doglogo from '../../assets/icon/icon_PETDY.png'
export default {
  components: {
    GmapCustomMarker,
    MarkerInfo
  },
  data () {
    return {
      selectedMarkerStatus: false,
      selectedindex: null,
      pathS3: process.env.VUE_APP_S3,
      butype: 'All',
      currentPositions: { lat: 0, lng: 0 },
      currentLat: null,
      currentLng: null,
      markers: [],
      selectedLAT: null,
      selectedLNG: null
    }
  },
  methods: {
    openMarkerInfoDialog (item, maptype, idx) {
      this.$store.commit('getshowMarkerStatusTRUE')
      this.selectedindex = idx
      this.$refs.MarkerInfo.openSheet(item, maptype)
    },
    async getMarker () {
      await this.getcurrentPosition()
      const body = {
        lat: this.currentLat,
        long: this.currentLng
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'v2/get_pet_location',
        body
      )
      console.log('result', result)

      await result.data.list_pet_location.sort(function (a, b) {
        return a.distance - b.distance
      })
      await result.data.list_pet_location.forEach(marker => {
        if (marker.distance < 50.0) {
          if (this.markers.length < 50) {
            this.markers.push({
              position: {
                lat: marker.latitude,
                lng: marker.longitude
              },
              id: marker.id,
              pic: marker.pic,
              name: marker.name,
              status: marker.status,
              type: marker.pettype,
              breed: marker.breed,
              gender: marker.gender,
              date_of_birth: marker.birthdate,
              weight: marker.weight,
              furcolor: marker.furcolor,
              updatetime: marker.update,
              realuser_id: marker.realuser_id
            })
          }
        }
      })
    },
    async getcurrentPosition () {
      const position = await this.getcurrentPosition2()
      this.currentLat = position.coords.latitude
      this.currentLng = position.coords.longitude
      this.currentPositions.lat = this.currentLat
      this.currentPositions.lng = this.currentLng
    },
    async getcurrentPosition2 () {
      this.currentPositions = []
      return new Promise(function (resolve, reject) {
        navigator.geolocation.getCurrentPosition(resolve, reject)
      })
    },
    clearMarker () {
      this.markers = []
    }
  },
  computed: {
    userID () {
      return JSON.parse(this.$ls.get('userprofile'))[0].user_id
    },
    markerStatus () {
      return this.$store.state.showMarkerStatus
    },
    update_location () {
      return this.$store.state.update_pet_location
    }
  },
  watch: {
    markerStatus (newValue) {
      this.$nextTick(() => {
        this.selectedMarkerStatus = newValue
      })
    },
    update_location (newValue) {
      if (newValue === true) {
        this.$nextTick(() => {
          this.getMarker()
        })
      }
    }
  },
  async mounted () {
    this.$store.commit('getshowMarkerStatusFALSE')
    this.userID = JSON.parse(this.$ls.get('userprofile'))[0].user_id
    this.clearMarker()
    this.getMarker()
  }
}
</script>

<style scoped>
.v-speed-dial--direction-top .v-speed-dial__list,
.v-speed-dial--direction-bottom .v-speed-dial__list {
  left: -35px !important;
}
.v-slide-group__prev {
  display: none !important;
}
.markerAvatar {
  margin-left: 3.5px;
  margin-top: 3.2px;
}
</style>
