<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="(dialog = false), closeDialog()">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Donate Blood</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-list three-line subheader>
          <v-container class=" mt-n3">
            <v-col cols="12" align="center">
              <UploadeImage ref="addpetDialog" v-bind:classupload="true"></UploadeImage>
              <v-container grid-list-xl> </v-container>
            </v-col>

            <v-row cols="12">
              <v-col cols="12">
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-text-field
                    v-model="data.title"
                    :rules="TopicRules"
                    outlined
                    label="Topic"
                    clearable
                    required
                  ></v-text-field>

                  <v-textarea
                    v-model="data.detail"
                    :rules="DetailRules"
                    label="Detail"
                    full-width
                    single-line
                    outlined
                  ></v-textarea>
                  <span class="txtsmall red--text"
                    >*Your Post automatically remove after 90 days</span
                  >
                </v-form>
              </v-col>
            </v-row>
          </v-container>

          <v-col>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Create'"
              @click="addpet()"
            >
              Save
            </v-btn>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Edit'"
              @click="edit()"
            >
              Save
            </v-btn>
          </v-col>
        </v-list>
      </v-card>
      <loading/>
    </v-dialog>
  </v-row>
</template>

<script>
import UploadeImage from '@/components/uploadeImage'
import loading from '@/components/Loading'
export default {
  components: {
    UploadeImage,
    loading
  },
  data () {
    return {
      valid: true,
      title: '',
      TopicRules: [v => !!v || 'Topic is required'],
      matchingplace: '',
      DetailRules: [v => !!v || 'Detail is required'],

      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      type: '',
      info: '',
      data: {
        title: '',
        picture: '',
        detail: '',
        user_create_id: '',
        post_id: ''
      }
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async addpet () {
      var formData = new FormData()
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        if (this.$store.state.imgBlob === null) {
          formData.append('file', '')
          formData.append('title', this.data.title)
          formData.append('detail', this.data.detail)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
        } else {
          formData.append('file', this.$store.state.imgBlob, 'png')
          formData.append('title', this.data.title)
          formData.append('detail', this.data.detail)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
        }
        await this.axios
          .post(process.env.VUE_APP_API + 'v2/add_blood_donate', formData)
          .then(res => {
            console.log('result', res)
          })
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      } else {
        console.log('can not create Donate Blood')
      }
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getupdateDonate')
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
    },
    async edit () {
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        var formData = new FormData()
        if (this.$store.state.imgBlob === null) {
          formData.append('file', '')
          formData.append('title', this.data.title)
          formData.append('detail', this.data.detail)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
          formData.append('post_id', this.data.post_id)
        } else {
          formData.append('file', this.$store.state.imgBlob, 'png')
          formData.append('title', this.data.title)
          formData.append('detail', this.data.detail)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
          formData.append('post_id', this.data.post_id)
        }
        await this.axios
          .post(process.env.VUE_APP_API + 'v2/edit_blood_donate', formData)
          .then(res => {
            console.log('result', res)
          })
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      } else {
        console.log('can not create Donate Blood')
      }
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getupdateDonate')
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
    },

    openDialog (info, type) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
      this.type = type
      if (type === 'Edit') {
        this.$store.commit('getshowImgstatusTRUE')
        this.$store.commit('getImgUrl', info.picture)
        this.data.title = info.title
        this.data.picture = info.picture
        this.data.detail = info.detail
        this.data.user_create_id = info.user_create_id
        this.data.post_id = info.post_id
      } else if (type === 'Create') {
        this.$store.commit('getshowImgstatusFALSE')
        this.$refs.form.reset()
        this.data.title = null
        this.data.detail = null
        this.data.picture = null
        this.data.user_create_id = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      }
    }
  }
}
</script>
<style scoped>
#p {
  word-spacing: 60px;
}

.uploadcard {
  border-style: dashed;
}
</style>
