<template>
  <v-dialog
    v-model="dialog"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-card>
      <v-toolbar dark color="primary" elevation="0">
        <v-btn icon dark @click="dialog = false">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-toolbar-title>User Profile</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-row class="mx-0">
        <v-col >
          <v-row justify="center" align="center" >
            <v-img
              :src="this.$store.state.imgUrl"
              max-width="250"
              max-height="250"
            >
            </v-img>
          </v-row>
        </v-col>
      </v-row>

      <v-card class="pa-2" elevation="0">
        <v-container>
          <v-row>
            <v-col cols="12" class="txtreg greyB--text">INFORMATION</v-col>
          </v-row>
          <v-row class="">
            <v-col>
              <v-row class="px-2 pb-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  Username
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0">
                  {{ JSON.parse(this.$ls.get('oneprofile')).username }}
                </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="px-2 py-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  Display name
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0">
                  {{ info.nickname }}
                </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="px-2 py-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  Name
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0">
                  {{ info.first_name_en }} {{ info.last_name_en }}
                </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="px-2 py-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  Mobile No
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0"> {{ info.mobile_no }} </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="px-2 py-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  one Mail
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0"> {{ info.oneemail }} </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="px-2 py-4 pl-3">
                <v-col cols="4" class="ma-0 pa-0 txtreg txtInfo">
                  Birth Date
                </v-col>
                <v-col cols="1" class="ma-0 pa-0 txtsmall">
                  :
                </v-col>
                <v-col cols="7" class="ma-0 pa-0">
                  {{ info.birthdate.split("T")[0] }}
                </v-col>
              </v-row>

              <v-divider></v-divider>

              <v-row class="mt-3">
                <v-col>
                  <v-btn
                    elevation="0"
                    block
                    color="primary"
                    class="txtbold"
                    @click="openEditUserProfile"
                    >Edit Profile</v-btn
                  >
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-card>
    <EditUserprofile ref="EditUserprofile"></EditUserprofile>
  </v-dialog>
</template>

<script>
// import UploadeImage from '@/components/uploadeImage'
import EditUserprofile from '@/components/Home/EditUserprofile'
import axios from 'axios'
export default {
  components: {
    // UploadeImage,
    EditUserprofile
  },
  data () {
    return {
      dialog: false,
      nickname: null,
      oneEmail: '',
      email: null,
      mobile: null,
      info: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    openEditUserProfile () {
      this.$refs.EditUserprofile.openDialog(this.info)
    },
    openDialog (info) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.setdata(info)
    },
    updated () {},
    setdata (info) {
      this.$nextTick(() => {
        this.info = info
        this.$store.commit('getshowImgstatusTRUE')
        this.$store.commit('getImgUrl', this.info.pic)
        this.nickname = this.info.nickname
        this.mobile = this.info.mobile_no
        this.email = this.info.email
      })
    },
    async getprofile () {
      const body2 = {
        one_id: JSON.parse(this.$ls.get('oneprofile')).one_id
      }
      const result2 = await axios.post(
        process.env.VUE_APP_API + 'get_user_by_oneid',
        body2
      )
      console.log('result : ', result2)
      localStorage.setItem(
        'userprofile',
        JSON.stringify(result2.data.user_data)
      )
      await this.setdata(JSON.parse(this.$ls.get('userprofile'))[0])
    }
  },
  computed: {
    updatUserprofile () {
      return this.$store.state.updatUserprofile
    }
  },
  watch: {
    updatUserprofile (newValue) {
      this.$nextTick(async () => {
        if (newValue === true) {
          await this.getprofile()
          this.$store.commit('getupdatUserprofile', false)
        }
      })
    }
  }
}
</script>

<style scoped>
.txtInfo {
  font-size: 14px;
  font-weight: bold;
}
</style>
