<template>
  <div>
    <v-toolbar dark color="primary" height="150">
      <v-row>
        <v-col cols="4">
          <v-row justify="center" align-content="center">
            <v-avatar size="50" color="greyF1" class="my-1">
              <img :src="userprofile.pic" />
            </v-avatar>
          </v-row>
        </v-col>

        <v-col cols="8" class="mt-2">
          <v-row justify="start" align-content="center">
            <read-more
              class="white--text txtreg"
              more-str=""
              :text="nickname"
              :max-chars="15"
            ></read-more>
          </v-row>
          <v-row justify="start" align-content="center">
            <read-more
              class="greyF1--text txtreg mt-n5"
              more-str=""
              :text="email"
              :max-chars="15"
            ></read-more>
          </v-row>
        </v-col>

        <v-col cols="12">
          <v-btn
            block
            outlined
            color="white"
            class="txtreg mt-n5"
            @click="openUserprofile(userprofile)"
            >Edit Profile</v-btn
          >
        </v-col>
      </v-row>
    </v-toolbar>

    <div class="pl-4 pt-5 txtreg greyB--text">MENU</div>
    <v-list v-for="(item, index) in menu" :key="index">
      <v-list-item @click="clickListItem(item.goto)">
        <div class="icon" :class="item.icon"></div>
        <div class="pl-3 pt-1 txtreg greyB--text">{{ item.title }}</div>
      </v-list-item>
    </v-list>
    <Userprofile ref="Userprofile"></Userprofile>
  </div>
</template>

<script>
import Userprofile from '@/components/Home/Userprofile'
import axios from 'axios'
export default {
  components: {
    Userprofile
  },
  data () {
    return {
      nickname: '',
      token: [],
      userImg: '../../assets/icon/user.png',
      oneID: null,
      userprofile: '',
      email: '',
      menu: [
        {
          icon: 'icon-home',
          title: 'Home',
          goto: 'https://wpdeveloper.inet.co.th/petdy/'
        },
        {
          icon: 'icon-tech',
          title: 'Technology',
          goto: 'https://wpdeveloper.inet.co.th/petdy/#technology'
        },
        {
          icon: 'icon-feature',
          title: 'Feature',
          goto: 'https://wpdeveloper.inet.co.th/petdy/#features'
        },
        {
          icon: 'icon-product',
          title: 'Product',
          goto: 'https://www.pacheti.com/'
        },
        {
          icon: 'icon-community',
          title: 'Community',
          goto: 'https://www.pacheti.com/'
        },
        {
          icon: 'icon-place',
          title: 'Place',
          goto: '/placedesktop'
        },
        {
          icon: 'icon-contact',
          title: 'Contact',
          goto: 'https://wpdeveloper.inet.co.th/petdy/#contact'
        }
      ]
    }
  },
  methods: {
    openUserprofile (info) {
      this.$refs.Userprofile.openDialog(info)
    },
    clickListItem (gotoURL) {
      window.location = gotoURL
    },
    async getprofile () {
      const body2 = {
        one_id: JSON.parse(this.$ls.get('oneprofile')).one_id
      }
      const result2 = await axios.post(
        process.env.VUE_APP_API + 'get_user_by_oneid',
        body2
      )
      console.log('result', result2)
      localStorage.setItem(
        'userprofile',
        JSON.stringify(result2.data.user_data)
      )
      this.userprofile = JSON.parse(this.$ls.get('userprofile'))[0]
      this.nickname = this.userprofile.nickname
      this.email = this.userprofile.email
    }
  },
  async mounted () {
    this.userprofile = JSON.parse(this.$ls.get('userprofile'))[0]
    this.nickname = this.userprofile.nickname
    this.email = this.userprofile.email
  },
  computed: {
    updatUserprofile () {
      return this.$store.state.updatUserprofile
    }
  },
  watch: {
    updatUserprofile (newValue) {
      this.$nextTick(() => {
        if (newValue === true) {
          this.getprofile()
          this.$store.commit('getupdatUserprofile', false)
        }
      })
    }
  }
}
</script>

<style scoped>
.icon-home {
  mask: url("../assets/icon/Mainmenu/Normal.svg");
}
.icon-tech {
  mask: url("../assets/icon/Mainmenu/Technology.svg");
}
.icon-feature {
  mask: url("../assets/icon/Mainmenu/Feature.svg");
}
.icon-product {
  mask: url("../assets/icon/Mainmenu/Collar.svg");
}
.icon-community {
  mask: url("../assets/icon/Mainmenu/Community.svg");
}
.icon-place {
  mask: url("../assets/icon/Mainmenu/Store.svg");
}
.icon-contact {
  mask: url("../assets/icon/Mainmenu/Phone.svg");
}
.icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  background: var(--v-greyC-base);
  mask-size: cover;
}
</style>
