<template>
  <v-container>
    <v-row >
      <v-col col="12" sm="12" class="pa-0" :class="$vuetify.breakpoint.xs ? '' : 'px-2' ">
         <v-data-iterator
      :items="donateData"
      :items-per-page="itemsPerPage"
      :page="page"
      hide-default-footer data-app
      loading
      loading-text=" ">
      <template v-slot:default="props" >
        <v-card tile class="pa-4 elevation-0 greyF1">
          <v-row class="px-2 ">
            <v-col cols="3" class="pa-3 " v-for="(item, index) in props.items"
            :key="index">
              <v-card height="237" class="elevation-0 brown--stoke">
                  <v-card-actions :class="$vuetify.breakpoint.xs ? 'pl-3' : 'pl-3'">
            <v-avatar @click="openFriendListdialog(item)" size="25px">
              <v-img :src="item.owner_pic"></v-img>
            </v-avatar>
            <div @click="openFriendListdialog(item)" class="body-1 ml-2 text_hide">{{item.owner_name}}</div>
            <v-spacer></v-spacer>

          </v-card-actions>
          <v-card-text @click="openDonateDetail(item)" class="text--primary pa-0 justify-center text-center">
            <v-avatar size="120px">
            <v-img v-if="item.picture !== ''" :src="item.picture" ></v-img>
            <v-img v-if="item.picture === ''" src="../../assets/icon/icon_PETDY.png"></v-img>
            </v-avatar>
            <div class="brown white--text txtbold mt-2">รับบริจาคเลือด</div>
            <div  class="grey--text txtsmall text-left px-2 text_hide">
              <v-icon small class="collocateIcon">mdi-comment-text-outline</v-icon>
                            <span class="ml-1 ">{{item.title}}</span>
            </div>
          </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </template>
<template v-slot:footer>
            <v-container grid-list-xs class="py-0 px-4
            ">
              <v-row>
                <v-col cols="4" class="text-left py-0">
                  <v-btn v-if="page !== 1" dark small outlined color="primary" class="mr-1" @click="formerPage">
                    <v-icon>mdi-chevron-left</v-icon>
                    Back
                  </v-btn>
                </v-col>
                <v-col cols="4" class="text-center py-0">
                  <span class="txtsmall greyB--text mt-6">
                    Page {{ page }} of {{ numberOfPages }}
                  </span>
                </v-col>
                <v-col cols="4" class="text-right py-0">
                  <v-btn v-if="page !== numberOfPages" dark small outlined color="primary" class="ml-1" @click="nextPage">
                    <v-icon>mdi-chevron-right</v-icon>
                    Next
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </template>
    </v-data-iterator>
    </v-col>

    </v-row>
     <DonateDetail ref="DonateDetail"/>
     <!-- <CreateDonate ref="CreateDonate"/>
      <FriendListdialog ref="FriendListdialog"/> -->

  </v-container>
</template>

<script>
import DonateDetail from '@/components_content/Community/DonateDetail'
// import CreateDonate from '@/components_content/Community/CreateDonate'
// import FriendListdialog from '@/components_content/Timeline/FriendListdialog'
import { mapState } from 'vuex'
import axios from 'axios'

export default {
  name: 'profilepetlost',
  components: {
    DonateDetail
    // CreateDonate,
    // FriendListdialog
  },
  created () {
    this.$emit('title', 'LostFound')
  },
  data () {
    return {
      menu: [
        {
          title: 'Edit Pet',
          eventclick: 'openCreateDonate'
        }
      ],
      pathS3: process.env.VUE_APP_S3,
      dialog: false,
      hidden: false,
      donateData: [],
      page: 1,
      itemsPerPage: 8,
      id: '',
      fab: false
    }
  },

  methods: {
    async getdonate () {
      const result = await axios.post(
        process.env.VUE_APP_API + 'v2/get_blood_donate'
      )
      console.log(result)
      this.donateData = result.data.list_donate
      result.data.list_donate.forEach(donate => {
        var result = donate.create_date.split('T')
        var result2 = result[1].split('.')
        donate.DAY = result[0]
        donate.TIME = result2[0]
      })
      this.id = JSON.parse(this.$ls.get('userprofile'))[0].user_id
    },
    openDonateDetail (info) {
      this.$refs.DonateDetail.openDialog(info)
    }
    // openCreateDonate (info, type) {
    //   this.$refs.CreateDonate.openDialog(info, type)
    //   console.log(type)
    //   console.log(info)
    // },
    // openFriendListdialog (info) {
    //   this.$refs.FriendListdialog.openDialog(info)
    // }
  },
  mounted () {
    this.getdonate()
  },

  watch: {
    updateDonate (newValue, oldValue) {
      // console.log(`Updating from ${oldValue} to ${newValue}`)
      if (newValue === true) {
        console.log('update')
        this.getdonate()
        this.$store.commit('getupdateDonateFALSE')
      }
    }
  },
  computed: mapState(['updateDonate'])

}
</script>
