<template>
  <div>
    <v-card
      v-for="(event, index) in event"
      :key="index"
      elevation="0"
      :color="event.color_noti"
      class="pa-2 my-5"
    >
      <v-card-title
        :class="
          event.color_noti == '#FDD835'
            ? 'txtbold black--text'
            : 'txtbold white--text'
        "
      >
        {{ event.title }}
      </v-card-title>
      <v-card-subtitle
        :class="
          event.color_noti == '#FDD835'
            ? 'pl-5 txtbold black--text'
            : 'pl-5 txtbold white--text'
        "
      >
        {{ event.datetime.split("T")[0] }}
        {{ event.datetime.split("T")[1].slice(0, 8) }}
      </v-card-subtitle>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ['event'],
  mounted () {
    console.log(this.event)
  },
  watch: {
    event (newValue) {
      console.log(newValue)
    }
  }
}
</script>

