<template>
  <div>
    <v-overlay :value="loader">
      <vue-loaders
        name="ball-spin-fade-loader"
        color="#60C9AC"
        scale="0.75"
        v-if="loading === true"
      ></vue-loaders>
    </v-overlay>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loader: false
    }
  },
  computed: {
    loading () {
      return this.$store.state.loading
    }
  },
  watch: {
    loading (newValue) {
      this.$nextTick(() => {
        if (newValue === true) {
          this.loader = this.$store.state.loading
        } else if (newValue === false) {
          this.loader = this.$store.state.loading
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped></style>
