<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="(dialog = false), closeDialog()">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Find Home</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-list three-line subheader>
          <v-container class=" mt-n3">
            <v-col cols="12" align="center">
              <UploadeImage ref="addpetDialog" v-bind:classupload="true"></UploadeImage>
              <!-- <v-container grid-list-xl>

    </v-container> -->
            </v-col>

            <v-row cols="12">
              <v-col cols="12">
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-text-field
                    v-model="data.detail"
                    :rules="DetailRules"
                    outlined
                    label="Detail"
                    clearable
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="data.contact"
                    :rules="MobileRules"
                    outlined
                    label="Mobile Number"
                    clearable
                  ></v-text-field>

                  <v-text-field
                    v-model="data.place"
                    :rules="LocationRules"
                    outlined
                    label="Location"
                    append-icon="mdi-map-marker"
                  ></v-text-field>
                </v-form>
              </v-col>
            </v-row>
            <span class="txtsmall red--text"
              >*Your Post automatically remove after 90 days</span
            >
          </v-container>

          <v-col>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Create'"
              @click="addpet()"
            >
              Save
            </v-btn>
            <v-btn
              elevation="0"
              block
              class="primary white--text mt-10"
              :disabled="!valid"
              color="primary"
              v-if="this.type === 'Edit'"
              @click="edit()"
            >
              Save
            </v-btn>
          </v-col>
        </v-list>
      </v-card>
      <loading/>
    </v-dialog>
  </v-row>
</template>

<script>
import UploadeImage from '@/components/uploadeImage'
import loading from '@/components/Loading'
export default {
  components: {
    UploadeImage,
    loading
  },
  data () {
    return {
      valid: true,
      title: '',
      DetailRules: [v => !!v || 'Detail is required'],

      contact: '',
      MobileRules: [
        v => !!v || 'Mobile Number is required',
        v => (v && v.length === 10) || 'The number of digits must be 10',
        v => /^[0-9]*$/.test(v) || 'this field must be numeric'
      ],
      Location: '',
      LocationRules: [v => !!v || 'Location is required'],

      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      type: '',
      info: '',
      id: '',
      data: {
        picture: '',
        detail: '',
        contact: '',
        place: '',
        user_create_id: '',
        post_id: ''
      }

    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async addpet () {
      var formData = new FormData()
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        if (this.$store.state.imgBlob === null) {
          formData.append('file', '')
          formData.append('detail', this.data.detail)
          formData.append('contact', this.data.contact)
          formData.append('place', this.data.place)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
        } else {
          formData.append('file', this.$store.state.imgBlob, 'png')
          formData.append('detail', this.data.detail)
          formData.append('contact', this.data.contact)
          formData.append('place', this.data.place)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
        }
        await this.axios
          .post(process.env.VUE_APP_API + 'v2/add_pet_find_home', formData)
          .then(res => {
            console.log('result', res)
          })
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      } else {
        console.log('can not create Find Home')
      }
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getupdateFindHome')
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
    },
    async edit () {
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        var formData = new FormData()
        if (this.$store.state.imgBlob === null) {
          formData.append('file', '')
          formData.append('detail', this.data.detail)
          formData.append('contact', this.data.contact)
          formData.append('place', this.data.place)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
          formData.append('post_id', this.data.post_id)
        } else {
          formData.append('file', this.$store.state.imgBlob, 'png')
          formData.append('detail', this.data.detail)
          formData.append('contact', this.data.contact)
          formData.append('place', this.data.place)
          formData.append('user_create_id', JSON.parse(this.$ls.get('userprofile'))[0].user_id)
          formData.append('post_id', this.data.post_id)
        }
        await this.axios
          .post(process.env.VUE_APP_API + 'v2/edit_pet_find_home', formData)
          .then(res => {
            console.log(res.data)
          })
        this.id = 1
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      } else {
        console.log('can not create Find Home')
      }
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getupdateFindHome')
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
    },
    openDialog (info, type) {
      this.$store.commit('getloading', true)
      this.info = info
      this.type = type
      this.dialog = true
      if (type === 'Edit') {
        this.$store.commit('getshowImgstatusTRUE')
        this.$store.commit('getImgUrl', info.picture)
        this.data.picture = info.picture
        this.data.detail = info.detail
        this.data.contact = info.contact
        this.data.place = info.place
        this.data.user_create_id = info.user_create_id
        this.data.post_id = info.post_id
      } else if (type === 'Create') {
        this.$store.commit('getshowImgstatusFALSE')
        this.$refs.form.reset()
        this.data.picture = null
        this.data.detail = null
        this.data.contact = null
        this.data.place = null
        this.data.user_create_id = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      }
    }
  }
}
</script>
<style scoped>
.uploadcard {
  border-style: dashed;
}
</style>
