<template>
  <v-container>
<!-- {{this.$store.state.updateDonate}} -->
    <v-row >
      <v-col col="12" sm="12" class="pa-0" :class="$vuetify.breakpoint.xs ? '' : 'px-2' ">
         <v-data-iterator
      :items="fromlostData"
      :items-per-page="itemsPerPage"
      :page="page"
      hide-default-footer data-app
      loading
      loading-text=" ">
      <template v-slot:default="props" >
        <v-card tile class="pa-4 elevation-0 greyF1">
          <v-row class="px-2 ">
            <v-col cols="3" class="pa-3 " v-for="(item, index) in props.items"
            :key="index">
              <v-card  class="elevation-0 shop--stoke">
                  <v-card-actions :class="$vuetify.breakpoint.xs ? 'pl-3' : 'pl-3'">
            <v-avatar @click="openFriendListdialog(item)" size="25px">
               <v-img :src="item.owner_pic" ></v-img>
            </v-avatar>
            <div @click="openFriendListdialog(item)"  class="body-1 ml-2 text_hide">{{item.owner_name}}</div>

            <v-spacer></v-spacer>

          </v-card-actions>
          <v-card-text @click="openLostFoundDetail(item)" class="text--primary pa-0 justify-center text-center">
            <v-avatar size="120px">
               <v-img :src="item.pic"></v-img>
            </v-avatar>
            <div class="shop white--text txtbold mt-2">{{ item.name }}</div>
            <div class="grey--text txtsmall text-left px-2">
              <v-icon small class="collocateIcon">mdi-clock-outline</v-icon>
              <span class="ml-1">{{item.DAY}}
                <!-- {{item.TIME}} -->
                </span>
            </div>
            <div class="grey--text txtsmall text-left px-2 pb-2 text_hide">
              <v-icon small class="collocateIcon">mdi-map-marker</v-icon>
              <span class="ml-1 ">{{ item.lastseenplace }}</span>
            </div>
          </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </template>
<template v-slot:footer>
            <v-container grid-list-xs class="py-0 px-4
            ">
              <v-row>
                <v-col cols="4" class="text-left py-0">
                  <v-btn v-if="page !== 1" dark small outlined color="primary" class="mr-1" @click="formerPage">
                    <v-icon>mdi-chevron-left</v-icon>
                    Back
                  </v-btn>
                </v-col>
                <v-col cols="4" class="text-center py-0">
                  <span class="txtsmall greyB--text mt-6">
                    Page {{ page }} of {{ numberOfPages }}
                  </span>
                </v-col>
                <v-col cols="4" class="text-right py-0">
                  <v-btn v-if="page !== numberOfPages" dark small outlined color="primary" class="ml-1" @click="nextPage">
                    <v-icon>mdi-chevron-right</v-icon>
                    Next
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </template>
    </v-data-iterator>
    </v-col>

    </v-row>
     <LostFoundDetail ref="LostFoundDetail"/>
      <!-- <CreateLostFound ref="CreateLostFound"/>
      <PetInformation ref="Petdetail"/>
      <FriendListdialog ref="FriendListdialog"/> -->

  </v-container>
</template>

<script>
import LostFoundDetail from '@/components_content/Community/LostFoundDetail'
// import CreateLostFound from '@/components_content/Community/CreateLostFound'
// import PetInformation from '@/components_content/Home/PetInformation'
// import FriendListdialog from '@/components_content/Timeline/FriendListdialog'

import { mapState } from 'vuex'

import axios from 'axios'

export default {
  name: 'profilepetlost',
  components: {
    LostFoundDetail
    // CreateLostFound,
    // PetInformation
    // FriendListdialog
  },

  created () {
    this.$emit('title', 'LostFound')
  },
  data () {
    return {
      menu: [
        {
          title: 'Edit Pet',
          eventclick: 'openCreateLostFound'
        },
        {
          title: 'Delete Pet',
          eventclick: 'openDeleteLostFound'
        }
      ],
      pathS3: process.env.VUE_APP_S3,
      dialog: false,
      hidden: false,
      fromlostData: [],
      page: 1,
      itemsPerPage: 8,
      id: '',
      fab: false
    }
  },

  methods: {
    async getlostfound () {
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_fromlost'
      )
      console.log(result)
      this.fromlostData = result.data.list_pet_fromlost
      result.data.list_pet_fromlost.forEach(fromlost => {
        var result = fromlost.datetime.split('T')
        var result2 = result[1].split('.')
        fromlost.DAY = result[0]
        fromlost.TIME = result2[0]
      })
      // this.id = this.$store.state.userID
    },
    async Remove (petid) {
      await this.axios.post(
        process.env.VUE_APP_API + 'remove_pet_fromlost', {
          pet_id: petid
        }
      ).then(res => { console.log(res.data) })
      this.getlostfound()
    },

    openLostFoundDetail (info) {
      this.$refs.LostFoundDetail.openDialog(info)
    }
    // openCreateLostFound (info, type) {
    //   this.$refs.CreateLostFound.openDialog(info, type)
    //   console.log(type)
    //   console.log(info)
    // },
    // openFriendListdialog (info) {
    //   this.$refs.FriendListdialog.openDialog(info)
    // }

  },
  mounted () {
    this.getlostfound()
  },
  watch: {
    updateLostFound (newValue, oldValue) {
      // console.log(`Updating from ${oldValue} to ${newValue}`)
      if (newValue === true) {
        console.log('update')
        this.getlostfound()
        this.$store.commit('getupdateLostFoundFALSE')
      }
    }
  },
  computed: mapState(['updateLostFound'])
}
</script>
