<template>
  <div>
    <v-card class="mt-1" v-if="nopost === true">
      <v-row>
        <v-col cols="12" class="pa-0 ma-0">
        <v-card v-if="nopost === true" class="mt-2" elevation="0" height="700">
          <v-row justify="center" class="mx-0">
            <v-col>
              <v-row justify="center" class="mt-16 mx-0">
                <v-card height="150" width="150" elevation="0">
                  <v-img src="../../../assets/icon/Device/bone.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center" class="mt-2 mx-0">
            <v-col cols="6">
              <v-row justify="center">
                <div class="txtbold" style="font-size:25px; color: #888888;">
                  No post yet
                </div>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
        </v-col>
      </v-row>
    </v-card>

    <v-container v-if="nopost === false" >
      <v-row>
        <v-col
          v-for="(post, index) in itempost"
          :key="index"
          class="d-flex child-flex pa-small "
          cols="4"
          sm="3"
          md="2"
        >
          <v-img
            @click="openTimelinedialog(post)"
            :src="post.picture"
            :lazy-src="post.picture"
            class="grey lighten-2"
          >
            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-row>
            </template>
          </v-img>
        </v-col>
      </v-row>
      <Timelinedialog ref="Timelinedialog" v-on:updatepost="getStory" />
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'
import Timelinedialog from '@/components/Timeline/Timelinedialog'
import { mapState } from 'vuex'
export default {
  components: {
    Timelinedialog
  },
  data () {
    return {
      itempost: [],
      nopost: false
    }
  },
  methods: {
    async getStory () {
      const body = {
        user_id: this.$store.state.userID,
        friend_id: this.$store.state.userID
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      if (result.data.list_post.length === 0) {
        this.$nextTick(() => {
          this.nopost = true
        })
      } else {
        this.$nextTick(() => {
          this.nopost = false
          this.itempost = result.data.list_post
        })
      }
    },
    openTimelinedialog (info) {
      this.$refs.Timelinedialog.openDialog(info)
    },
    openPostinfoDialog (info) {
      this.$refs.Postinfo.openDialog(info)
    }
  },
  async created () {
    await this.getStory()
  },
  // async mounted () {
  //   await this.getStory()
  // },
  computed: mapState(['updatePost']),
  watch: {
    async updatePost (newValue) {
      if (newValue === true) {
        await this.getStory()
        this.$store.commit('getupdatePost', false)
      }
    }
  }
}
</script>

<style scoped>
.pa-small {
  padding: 2px;
}
.v-progress-circular {
  margin: 1rem;
}
</style>
