<template>
  <div>
    <v-card color="white" min-height="822">
      <v-toolbar dark color="primary">
        <v-toolbar-title class="pl-7">Register</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-stepper v-model="e1" class="elevation-0">
        <v-stepper-header class="elevation-0">
          <template v-for="n in steps">
            <v-stepper-step
              :key="`${n}-step`"
              :complete="e1 > n"
              :step="n"
              edit-icon="mdi-check"
              editable
              color="secondary"
            >
            </v-stepper-step>

            <v-divider v-if="n !== steps" :key="n"></v-divider>
          </template>
        </v-stepper-header>

        <v-stepper-items class="mt-n5">
          <v-stepper-content v-for="n in steps" :key="`${n}-content`" :step="n">
            <v-card tile elevation="0" v-if="n === 1">
              <UploadeImage
                ref="addpetDialog"
                v-bind:width="250"
                v-bind:height="250"
                v-bind:classupload="true"
              ></UploadeImage>
              <v-form ref="form" v-model="valid" lazy-validation>
                <div class="txtbold primary--text pb-3">Name for Display</div>
                <v-row class="mx-0">
                  <v-textarea
                    clearable
                    :rules="requireRule"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="nickname"
                    label="Nickname"
                  ></v-textarea>
                </v-row>

                <div class="txtbold primary--text pb-3">Contact</div>

                <v-row class="mx-0">
                  <v-textarea
                    disabled
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="oneEmail"
                    label="One Email"
                  ></v-textarea>
                  <v-textarea
                    :rules="emailRules"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="email"
                    label="Email"
                  ></v-textarea>

                  <v-textarea
                    :rules="numericRules"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="mobile"
                    label="Mobile Number"
                  ></v-textarea>
                </v-row>
              </v-form>
            </v-card>

            <v-card tile elevation="0" v-if="n === 2">
              <v-form ref="form" v-model="valid" lazy-validation>
                <div class="txtbold primary--text pb-3">Name (TH)</div>
                <v-row class="mx-0">
                  <v-autocomplete
                    :rules="requireRule"
                    v-model="prefixNameTH"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Prefix Name (TH)"
                    :items="prefixnameTHlist"
                    clearable
                  ></v-autocomplete>

                  <v-textarea
                    :rules="requireRule"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="firstNameTH"
                    label="First Name (TH)"
                  ></v-textarea>

                  <v-textarea
                    :rules="requireRule"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="lastNameTH"
                    label="Last Name (TH)"
                  ></v-textarea>
                </v-row>
                <div class="txtbold primary--text pb-3">Name (EN)</div>
                <v-row class="mx-0">
                  <v-autocomplete
                    :rules="requireRule"
                    v-model="prefixNameEN"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Prefix Name (EN)"
                    :items="prefixnameENlist"
                    clearable
                  ></v-autocomplete>

                  <v-textarea
                    :rules="requireRule"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="firstNameEN"
                    label="First Name (EN)"
                  ></v-textarea>

                  <v-textarea
                    :rules="requireRule"
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="lastNameEN"
                    label="Last Name (EN)"
                  ></v-textarea>
                </v-row>
                <div class="txtbold primary--text pb-3">Gender</div>
                <v-row class="mx-0">
                  <v-autocomplete
                    :rules="requireRule"
                    v-model="gender"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Gender"
                    :items="genderlist"
                    clearable
                  ></v-autocomplete>
                </v-row>
                <div class="txtbold primary--text pb-3">Birth Date</div>
                <v-row class="mx-0">
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        :rules="requireRule"
                        v-model="birthDate"
                        outlined
                        dense
                        label="Birth Date"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      ref="picker"
                      v-model="birthDate"
                      @change="closemenu"
                    ></v-date-picker>
                  </v-menu>
                </v-row>
              </v-form>
            </v-card>

            <v-card tile elevation="0" v-if="n === 3">
              <v-form ref="form" v-model="valid" lazy-validation>
                <div class="txtbold primary--text pb-3">Country</div>
                <v-row class="mx-0">
                  <v-autocomplete
                    :rules="requireRule"
                    v-model="country"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Country"
                    :items="countryList"
                    item-text="name_en"
                    clearable
                    @change="selectCountry(country)"
                  ></v-autocomplete>
                </v-row>
                <div class="txtbold primary--text pb-3">
                  Address Information
                </div>
                <v-row class="mx-0" v-if="country === 'Thailand'">
                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="address"
                    label="Address"
                  ></v-textarea>

                  <v-autocomplete
                    v-model="province"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Province"
                    :items="provinceList"
                    item-text="name_in_english"
                    clearable
                    @change="selectProvince(province)"
                  ></v-autocomplete>

                  <v-autocomplete
                    v-model="district"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="District"
                    :items="districtList"
                    item-text="name_in_english"
                    clearable
                    @change="selectDistrict(district)"
                  ></v-autocomplete>

                  <v-autocomplete
                    v-model="subdistrict"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Subdistrict"
                    :items="subdistrictList"
                    item-text="name_in_english"
                    clearable
                    @change="selectSubdistrict(subdistrict)"
                  ></v-autocomplete>

                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="zipCode"
                    label="Zip code"
                  ></v-textarea>
                </v-row>

                <v-row class="mx-0" v-if="country !== 'Thailand'">
                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="address"
                    label="Address"
                  ></v-textarea>

                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="province"
                    label="Province"
                  ></v-textarea>

                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="district"
                    label="District"
                  ></v-textarea>

                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="subdistrict"
                    label="Subdistrict"
                  ></v-textarea>

                  <!-- <v-autocomplete
                    v-model="province"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Province"
                    :items="provinceList"
                    item-text="name_in_english"
                    clearable
                    @change="selectProvince(province)"
                  ></v-autocomplete> -->

                  <!-- <v-autocomplete
                    v-model="district"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="District"
                    :items="districtList"
                    item-text="name_in_english"
                    clearable
                    @change="selectDistrict(district)"
                  ></v-autocomplete> -->

                  <!-- <v-autocomplete
                    v-model="subdistrict"
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    label="Subdistrict"
                    :items="subdistrictList"
                    item-text="name_in_english"
                    clearable
                    @change="selectSubdistrict(subdistrict)"
                  ></v-autocomplete> -->

                  <v-textarea
                    clearable
                    auto-grow
                    outlined
                    rows="1"
                    row-height="15"
                    dense
                    v-model="zipCode"
                    label="Zip code"
                  ></v-textarea>
                </v-row>
              </v-form>
            </v-card>

            <v-row>
              <v-col cols="6">
                <v-btn
                  v-if="n === 1"
                  auto-grow
                  outlined
                  rows="1"
                  row-height="15"
                  elevation="0"
                  block
                  color="greyB"
                  @click="cancel"
                  >Cancel</v-btn
                >
                <v-btn
                  v-if="n === 2 || n === 3"
                  auto-grow
                  outlined
                  rows="1"
                  row-height="15"
                  elevation="0"
                  block
                  color="primary"
                  @click="previouseStep(n)"
                  >Previous</v-btn
                >
              </v-col>

              <v-col cols="6">
                <v-btn
                  v-if="n === 1 || n === 2"
                  elevation="0"
                  block
                  color="primary"
                  @click="nextStep(n)"
                  :disabled="!valid"
                  >Next</v-btn
                >
                <v-btn
                  v-if="n === 3"
                  elevation="0"
                  block
                  color="primary"
                  @click="register(n)"
                  :disabled="!valid"
                  >Regist</v-btn
                >
              </v-col>
            </v-row>

            <v-dialog v-model="dialog">
              <v-card v-if="correctForm === true">
                <sweetalert-icon icon="success"></sweetalert-icon>
                <v-card-title class="justify-center"
                  >Register success !!</v-card-title
                >
              </v-card>
              <v-card v-if="correctForm === false">
                <sweetalert-icon icon="warning"></sweetalert-icon>
                <v-card-title class="justify-center"
                  >Please fill in all field</v-card-title
                >
              </v-card>
            </v-dialog>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card>
  </div>
</template>

<script>
import UploadeImage from '@/components/uploadeImage'
import SweetalertIcon from 'vue-sweetalert-icons'
import axios from 'axios'
export default {
  components: {
    UploadeImage,
    SweetalertIcon
  },
  data () {
    return {
      menu: false,
      oneIDfromlocal: '',
      oneEmailfromlocal: '',
      oneUsernamefromlocal: '',

      countrylist: ['Thai', 'Other'],

      correctForm: null,
      dialog: false,
      valid: true,
      e1: 1,
      steps: 3,

      nickname: null,
      // oneEmail: 'natawat14277@one.th',
      oneEmail: JSON.parse(this.$ls.get('oneprofile')).email,
      email: null,
      mobile: null,

      prefixNameTH: null,
      prefixnameTHlist: ['นาย', 'นางสาว', 'นาง'],
      firstNameTH: null,
      lastNameTH: null,
      prefixNameEN: null,
      prefixnameENlist: ['Mr', 'Mrs', 'Miss'],
      firstNameEN: null,
      lastNameEN: null,
      gender: null,
      genderlist: ['Male', 'Female'],
      birthDate: null,

      country: '',
      countryID: '',
      address: '',
      province: '',
      provinceID: '',
      district: '',
      districtID: '',
      subdistrict: '',
      subdistrictID: '',
      zipCode: '',

      countryList: [],
      provinceList: [],
      districtList: [],
      subdistrictList: [],

      GENDER_prefix: '',

      requireRule: [v => !!v || 'This is required'],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],

      numericRules: [
        v => !!v || 'This is required',
        v => /^[0-9]*$/.test(v) || 'this field must be numeric'
      ]
    }
  },
  updated: function () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  async mounted () {
    await this.getcountry()
    await this.getprovince()
  },
  methods: {
    closemenu (date) {
      this.menu = false
    },
    selectCountry (data) {
      this.countryList.forEach(focus => {
        if (data === focus.name_en) {
          this.countryID = focus.id
        }
      })
    },
    async selectProvince (data) {
      this.provinceList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.provinceID = focus.id
        }
      })
      await this.getdistrict(this.provinceID)
    },
    async selectDistrict (data) {
      this.districtList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.districtID = focus.id
        }
      })
      await this.getsubdistrict(this.districtID)
    },
    selectSubdistrict (data) {
      this.subdistrictList.forEach(focus => {
        if (data === focus.name_in_english) {
          this.subdistrictID = focus.id
        }
      })
    },
    async getcountry () {
      this.countryList = []
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_countries'
      )
      this.countryList = result.data.countries_data
    },
    async getprovince () {
      this.provinceList = []
      const result = await axios.post(process.env.VUE_APP_API + 'get_provices')
      this.provinceList = result.data.provices_data
    },
    async getdistrict (id) {
      this.districtList = []
      const body = {
        province_id: id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_districts',
        body
      )
      console.log(result)
      this.districtList = result.data.districts_data
    },
    async getsubdistrict (id) {
      this.subdistrictList = []
      const body = {
        district_id: id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_subdistricts',
        body
      )
      this.subdistrictList = result.data.subdistricts_data
    },
    nextStep (n) {
      if (this.$refs.form[n - 1].validate() === true) {
        this.e1 = n + 1
      }
    },
    closeDialog () {
      this.dialog = false
    },
    async register (n) {
      var formData = new FormData()
      if (
        this.$refs.form[n - 1].validate() === true &&
        this.$refs.form[n - 2].validate() === true &&
        this.$refs.form[n - 3].validate() === true
      ) {
        this.correctForm = true
        this.dialog = true

        setTimeout(this.closeDialog, 1000)

        this.oneIDfromlocal = JSON.parse(this.$ls.get('oneprofile')).one_id
        this.oneEmailfromlocal = JSON.parse(this.$ls.get('oneprofile')).email
        this.oneUsernamefromlocal = JSON.parse(
          this.$ls.get('oneprofile')
        ).username

        if (this.gender === 'Male') {
          this.GENDER_prefix = 'M'
        } else if (this.gender === 'Female') {
          this.GENDER_prefix = 'F'
        }

        if (this.$store.state.imgBlob === null) {
          formData.append('file', '')
          formData.append('username', this.oneUsernamefromlocal)
          formData.append('one_id', this.oneIDfromlocal)
          formData.append('nickname', this.nickname)
          formData.append('account_title_th', this.prefixNameTH)
          formData.append('account_title_en', this.prefixNameEN)
          formData.append('first_name_th', this.firstNameTH)
          formData.append('first_name_en', this.firstNameEN)
          formData.append('last_name_th', this.lastNameTH)
          formData.append('last_name_en', this.lastNameEN)
          formData.append('gender', this.GENDER_prefix)
          formData.append('mobile_no', this.mobile)
          formData.append('email', this.email)
          formData.append('birthdate', this.birthDate)
          formData.append('address', this.address)
          formData.append('zipcode', this.zipCode)
          formData.append('country_id', this.countryID)
          formData.append('districts_id', this.districtID)
          formData.append('province_id', this.provinceID)
          formData.append('subdistrict_id', this.subdistrictID)
          formData.append('country', this.country)
          formData.append('district', this.district)
          formData.append('province', this.province)
          formData.append('subdistrict', this.subdistrict)
          formData.append('facebookid', '')
          formData.append('oneemail', this.oneEmailfromlocal)
        } else {
          formData.append('file', this.$store.state.imgBlob, 'png')
          formData.append('username', this.oneUsernamefromlocal)
          formData.append('one_id', this.oneIDfromlocal)
          formData.append('nickname', this.nickname)
          formData.append('account_title_th', this.prefixNameTH)
          formData.append('account_title_en', this.prefixNameEN)
          formData.append('first_name_th', this.firstNameTH)
          formData.append('first_name_en', this.firstNameEN)
          formData.append('last_name_th', this.lastNameTH)
          formData.append('last_name_en', this.lastNameEN)
          formData.append('gender', this.GENDER_prefix)
          formData.append('mobile_no', this.mobile)
          formData.append('email', this.email)
          formData.append('birthdate', this.birthDate)
          formData.append('address', this.address)
          formData.append('zipcode', this.zipCode)
          formData.append('country_id', this.countryID)
          formData.append('districts_id', this.districtID)
          formData.append('province_id', this.provinceID)
          formData.append('subdistrict_id', this.subdistrictID)
          formData.append('country', this.country)
          formData.append('district', this.district)
          formData.append('province', this.province)
          formData.append('subdistrict', this.subdistrict)
          formData.append('facebookid', '')
          formData.append('oneemail', this.oneEmailfromlocal)
        }

        const result = await axios.post(
          process.env.VUE_APP_API + 'v2/register_user',
          formData
        )
        console.log(result)
        this.$store.commit('getimgBlob', null)
        this.$router.push('/')
      } else {
        this.correctForm = false
        this.dialog = true
        setTimeout(this.closeDialog, 1000)
      }
    },
    cancel () {
      console.log('Cancel')
    },
    previouseStep (n) {
      this.e1 = n - 1
    }
  }
  // async beforeCreate () {
  //   var paramsString = window.location.href
  //   var onetoken = []
  //   var searchParams = new URLSearchParams(paramsString)
  //   searchParams.forEach(token => {
  //     onetoken.push(token)
  //     if (onetoken[0] !== '') {
  //       this.$ls.set('token', onetoken[0])
  //     }
  //   })

  //   const body = {
  //     source: this.$ls.get('token')
  //   }
  //   const headers = {
  //     Authorization:
  //       'Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1'
  //   }

  //   const result = await axios.post(
  //     'https://chat-api.one.th/manage/api/v1/login_onechat_token',
  //     body,
  //     { headers }
  //   )

  //   const body2 = {
  //     one_id: result.data.account.profile.one_id
  //   }

  //   const result2 = await axios.post(
  //     process.env.VUE_APP_API + 'get_user_by_oneid',
  //     body2
  //   )

  //   if (result2.data.message === undefined) {
  //     this.$router.push('/register')
  //   } else {
  //     this.$router.push('/main')
  //   }
  // }
}
</script>

<style lang="scss" scoped>
.registerList {
  top: -100px;
  height: 200px; /* or any height you want */
  overflow-y: auto;
}
</style>
