<template>
    <v-dialog
      v-model="dialog"
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card color="white">
        <v-toolbar dark color="primary" class="toolbar1">
          <v-btn icon dark @click="dialog = false" >
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title class="pl-0">{{date}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="openCreateevent">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-toolbar>
        <v-container >
            <ReminderSubCard v-bind:event="todayEvent"/>
        </v-container>
      </v-card>
      <CreateEvent ref="CreateEvent" v-on:updateEvent="updateEvents"/>
    </v-dialog>
</template>

<script>
import ReminderSubCard from '@/components/Home/Mypet/Calendar/ReminderSubCard'
import CreateEvent from '@/components/Home/Mypet/Calendar/CreateEvent'
export default {
  components: {
    ReminderSubCard,
    CreateEvent
  },
  data () {
    return {
      dialog: false,
      date: null,
      todayEvent: null,
      info: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    openCreateevent () {
      this.$refs.CreateEvent.openDialog(this.info, '', 'createEvent', this.date)
    },
    openDialog (info, date, todayEvent) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
      this.date = date
      this.todayEvent = todayEvent
      console.log(this.date)
      console.log(this.todayEvent)
    }
  }

}

</script>

<style scoped>
.toolbar1{
  position: sticky;
  top: 0;
  z-index: 999;
}
</style>
