<template>
  <div>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card color="#333333">
        <v-toolbar dark color="primary" elevation="0">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>QR scanner</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <QRScaner v-if="dialog=== true" v-bind:ScanAction="'petprofile'" v-on:closeDialog=" dialog = false "/>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import QRScaner from '@/components/QRScaner'
export default {
  name: 'qrscanner_dialog',
  components: {
    QRScaner
  },
  data () {
    return {
      dialog: false
    }
  },
  methods: {
    openDialog () {
      this.dialog = true
    }
  }
}
</script>

<style lang="scss" scoped></style>
