<template>
  <v-container class="px-3 pt-2 py-0 white">
    <v-row>
      <v-col class="pt-1 pb-4" cols="4" sm="3" md="2">
        <v-avatar size="110">
          <img :src="userImg" @click="openUserprofile(userprofile)">
        </v-avatar>
      </v-col>
      <v-col class="pl-0 pt-1 pb-4" cols="8" sm="9" md="10">
        <v-row class="mt-3 pl-6 pb-0 mb-0">
          <v-col class="my-0 py-0">
            <read-more class="secondary--text txtbold" style="font-size: 22px;" more-str="" :text="nickName" :max-chars="20"></read-more>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0 text-center" cols="4">
            <h2 class="txtbold" style="font-size: 20px;">{{numOfPost}}</h2>
            <div class="txtsmall pt-1">Posts</div>
          </v-col>
          <v-col class="py-0 pl-0 text-center" cols="4">
            <h2 class="txtbold" style="font-size: 20px;">{{numOfFollowing}}</h2>
            <div class="txtsmall pt-1">Following</div>
          </v-col>
          <v-col class="py-0 pl-0 text-center" cols="4">
            <h2 class="txtbold" style="font-size: 20px;">{{numOfFollower}}</h2>
            <div class="txtsmall pt-1">Followers</div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <Userprofile ref="Userprofile"></Userprofile>
  </v-container>

</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import Userprofile from '@/components/Home/Userprofile'
export default {
  components: {
    Userprofile
  },
  data () {
    return {
      token: [],
      userImg: '../../assets/icon/user.png',
      nickName: '',
      oneID: null,
      itempet: null,
      numOfPost: null,
      numOfFollowing: null,
      numOfFollower: null,
      oneprofile: '',
      userprofile: '',
      lstoken: this.$ls.get('token')
    }
  },
  methods: {
    openUserprofile (info) {
      this.$refs.Userprofile.openDialog(info[0])
    },
    async getStory () {
      const body = {
        user_id: this.$store.state.userID,
        friend_id: this.$store.state.userID
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      this.numOfPost = result.data.list_post.length
    },
    async getFollow () {
      const body = {
        user_id: this.$store.state.userID,
        friend_id: this.$store.state.userID
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_user_follow',
        body
      )
      this.numOfFollowing = result.data.following_count
      this.numOfFollower = result.data.follower_count
    },
    async getMypet () {
      const body = {
        user_id: this.$store.state.userID,
        friend_id: this.$store.state.userID
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_by_userid',
        body
      )
      this.itempet = result.data.pet_data
    },
    setProfile () {
      this.userImg = this.oneprofile.pic
      this.nickName = this.oneprofile.nickname
    },
    async getprofile () {
      const body2 = {
        one_id: JSON.parse(this.$ls.get('oneprofile')).one_id
      }
      const result2 = await axios.post(
        process.env.VUE_APP_API + 'get_user_by_oneid',
        body2
      )
      console.log(result2)
      localStorage.setItem('userprofile', JSON.stringify(result2.data.user_data))
      this.oneprofile = JSON.parse(this.$ls.get('userprofile'))[0]
      this.userprofile = JSON.parse(this.$ls.get('userprofile'))
      this.userImg = this.oneprofile.pic
      this.nickName = this.oneprofile.nickname
    }
  },
  async mounted () {
    this.oneprofile = JSON.parse(this.$ls.get('userprofile'))[0]
    this.userprofile = JSON.parse(this.$ls.get('userprofile'))
    this.setProfile()
    this.$store.commit('getuserID', this.userprofile[0].user_id)
    this.getStory()
    this.getFollow()
    await this.getMypet()
    this.$store.commit('getitemMypet', this.itempet)
  },
  computed: mapState(['updateprofile', 'token', 'updateFriendList', 'updatUserprofile']),
  watch: {
    updateprofile (newValue, oldValue) {
      if (newValue === true) {
        this.getStory()
        this.$store.commit('getupdateprofile', false)
      }
    },
    updateFriendList (newValue) {
      if (newValue === true) {
        this.getFollow()
        this.$store.commit('getupdateFriendListFALSE')
      }
    },
    async updatUserprofile (newValue) {
      this.$nextTick(() => {
        if (newValue === true) {
          this.getprofile()
          this.$store.commit('getupdatUserprofile', false)
        }
      })
    }
  },
  updated () {
    this.lstoken = this.$ls.get('token')
  }
}
</script>
