import Vue from 'vue'
import store from '../store'
// import axios from 'axios'
import VueRouter from 'vue-router'
import Header from '../components/Header.vue'
import HeaderTitle from '../components/HeaderTitle.vue'
import HeaderTitlecontent from '../components_content/HeaderTitle.vue'

import Home from '../views/Home.vue'
import Timeline from '../views/Timeline.vue'

import Community from '../views/Community.vue'
import Communitycontent from '../views_content/Community.vue'

import CommunityBlogType from '../views/CommunityBlogType.vue'
import CommunityBlogTypecontent from '../views_content/CommunityBlogType.vue'
import Place from '../views/Place.vue'
import placeDesktop from '../views_content/PlaceDesktop.vue'
import Register from '../views/Register.vue'
import PetprofilefromQR from '../views/Petprofile_fromQR.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/content/community/:tab',
    name: 'Communitydesktop',
    component: Communitycontent
  },
  {
    path: '/pet_profile/:token',
    name: 'PetprofilefromQR',
    component: PetprofilefromQR
  },
  {
    path: '/',
    name: 'Header',
    component: Header,
    children: [
      {
        path: '/',
        name: 'Home',
        component: Home
      },
      {
        path: '/Home',
        name: 'Home',
        component: Home
      },
      {
        path: '/timeline',
        name: 'Timeline',
        component: Timeline
      },
      {
        path: '/community/:tab',
        name: 'Community',
        component: Community
      },
      {
        path: '/place',
        name: 'Place',
        component: Place
      }
    ]
  },
  {
    path: '/',
    name: 'HeaderTitle1',
    component: HeaderTitle,
    children: [
      {
        path: 'community/blog/:type',
        name: 'CommunityBlogType1',
        component: CommunityBlogType
      },
      {
        path: 'community/blog/detail/:id',
        name: 'CommunityBlogType2',
        component: CommunityBlogType
      }
    ]
  },
  {
    path: '/content',
    name: 'HeaderTitle2',
    component: HeaderTitlecontent,
    children: [
      {
        path: '/community/blog/:type',
        name: 'CommunityBlogType3',
        component: CommunityBlogTypecontent
      },
      {
        path: '/community/petbreed/:type',
        name: 'CommunityBlogType4',
        component: CommunityBlogTypecontent
      },
      {
        path: '/community/blog/detail/:id',
        name: 'CommunityBlogType5',
        component: CommunityBlogTypecontent
      },
      {
        path: '/place',
        name: 'placedesktop',
        component: placeDesktop
      }
    ]
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/content/place',
    name: 'placedesktop',
    component: placeDesktop
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  if (to.name) {
    store.commit('getloading', true)
    next()
  }
  if (from.name === null && to.name === 'Home') {
    localStorage.clear()
  }
  if (to.name !== 'Home') {
    store.commit(
      'getuserID',
      JSON.parse(localStorage.getItem('userprofile'))[0].user_id
    )
  }
  next()
})

