<template>
  <v-chip-group multiple>
    <v-badge
      class="mx-3"
      color="primary"
      icon="mdi-plus"
      bottom
      bordered
      offset-x="20"
      offset-y="20"
    >
      <v-avatar
        @click="openAddstory('', 'createpost')"
        size="65"
        class="stokeImgMe"
      >
        <v-img :src="userprofile.pic"></v-img>
      </v-avatar>
    </v-badge>
    <v-data-iterator
      :items="userfollowData"
      :items-per-page="itemsPerPage"
      :page="page"
      hide-default-footer
      data-app
      loading
      loading-text=""
      v-if="userfollowData.length !== 0"
    >
      <template v-slot:default="props">
        <v-row class="px-1">
          <v-col
            class="py-0 pl-0 pr-2"
            col="12"
            sm="6"
            md="4"
            v-for="(item, index) in props.items"
            :key="index"
          >
            <v-avatar
              @click="openFriendListdialog(item)"
              size="65"
              class="mr-2 stokeImg"
            >
              <v-img :src="item.owner_pic"></v-img>
            </v-avatar>
          </v-col>
        </v-row>
      </template>
    </v-data-iterator>

    <v-avatar @click="openSearchUser()" size="65" class="mr-2 stokegrey" v-if="userfollowData.length !== 0">
      <v-icon dark color="greyB" size="30">
        mdi-plus
      </v-icon>
    </v-avatar>

    <v-row class="px-1" v-if="userfollowData.length === 0">
      <v-col class="pa-0" col="12" sm="6" md="4">
        <v-avatar @click="openSearchUser()" size="65" class="mr-2 stokegrey">
          <v-icon dark color="greyB" size="30">
            mdi-plus
          </v-icon>
        </v-avatar>
      </v-col>
    </v-row>
    <FriendListdialog ref="FriendListdialog" />
    <Addstory ref="Addstory" />
    <MenuSearch ref="MenuSearch" />
  </v-chip-group>
</template>

<script>
import axios from 'axios'
import FriendListdialog from '@/components/Timeline/FriendListdialog'
import Addstory from '@/components/Home/Mystory/Addstory'
import { mapState } from 'vuex'
import MenuSearch from '@/components/Searchuser/MenuSearch'
export default {
  name: 'Timeline',
  components: {
    FriendListdialog,
    Addstory,
    MenuSearch
  },
  data () {
    return {
      userfollowData: [],
      post_id: '',
      picture: '',
      caption: '',
      create_date: '',
      edited_date: '',
      nickname: '',
      page: 1,
      itemsPerPage: 99999,
      userprofile: null
    }
  },
  methods: {
    openSearchUser () {
      this.$refs.MenuSearch.openDialog()
    },
    async getTimeline () {
      const body = {
        user_id: this.userprofile.user_id,
        friend_id: this.userprofile.user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_user_follow',
        body
      )
      this.userfollowData = result.data.list_following
      console.log(this.userfollowData)
    },
    openTimelinedialog (info) {
      this.$refs.Timelinedialog.openDialog(info)
    },
    openFriendListdialog (info) {
      this.$refs.FriendListdialog.openDialog(info)
    },
    numberid (num) {
      this.$store.commit('getnumberid', num)
      this.$emit('update', num)
    },
    openAddstory (info, todo) {
      this.$refs.Addstory.openDialog(info, todo)
    }
  },
  mounted () {
    this.userprofile = JSON.parse(this.$ls.get('userprofile'))[0]
    this.getTimeline()
  },
  watch: {
    updateFriendList (newValue, oldValue) {
      if (newValue === true) {
        this.getTimeline()
        this.$store.commit('getupdateFriendListFALSE')
      }
    }
  },
  computed: mapState(['updateFriendList'])
}
</script>

<style scoped>
.stokeImg {
  border: 3px solid var(--v-secondary-base);
}
.stokeImgMe {
  border: 3px solid var(--v-primary-base);
}
.stokegrey {
  border: 2px solid var(--v-greyF1-base);
}
</style>
