<template>
  <div>
    <v-app id="inspire">
      <v-app-bar app color="primary" elevation="2" tile>
        <img src="../assets/logo.png" height="70%" />
      </v-app-bar>

      <v-card tile elevation="0" color="greyF1 ">
        <v-card class="pa-2" tile elevation="0">
          <v-row class="mx-0">
            <v-col cols="12" sm="3" md="2">
              <v-row align="center" justify="center">
                <v-avatar size="130" rounded="circle">
                  <v-img :src="pet_data.pic_pet"></v-img>
                </v-avatar>
              </v-row>
            </v-col>
          </v-row>

          <v-row class="mx-0">
            <v-col class="py-0" cols="12" sm="9" md="10">
              <v-row align="center" justify="center">
                <b class="primary--text" style="font-size:20px;">{{
                  pet_data.name_pet
                }}</b>
              </v-row>

              <v-row align="center" justify="center">
                <b class="greyC--text txtInfo"
                  >ยินดีที่ได้รู้จัก ขอต้อนรับเข้าสู่หน้าแสดงข้อมูลของฉัน</b
                >
              </v-row>
            </v-col>
          </v-row>
        </v-card>
        <v-card tile elevation="0" class="mt-1 pa-2">
          <v-container class="py-0">
            <v-row>
              <v-col>
                <span class="txtreg greyB--text">STATUS :</span>
                <v-chip
                  color="hospital"
                  text-color="white"
                  class="ml-5"
                  v-if="pet_data.status === 1"
                  >Normal</v-chip
                >
                <v-chip
                  color="shop"
                  text-color="white"
                  class="ml-5"
                  v-if="pet_data.status === 0"
                  >Lost</v-chip
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" class="txtreg greyB--text">INFORMATION</v-col>
            </v-row>

            <v-row>
              <v-col cols="4" class="txtreg txtInfo">Breed</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7">{{ pet_data.breed }}</v-col>
            </v-row>

            <v-divider></v-divider>
            <v-row>
              <v-col cols="4" class="txtreg txtInfo">Gender</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7">{{ pet_data.gender }}</v-col>
            </v-row>

            <v-divider></v-divider>
            <v-row>
              <v-col cols="4" class="txtreg txtInfo">Fur Color</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7">{{ pet_data.furcolor }}</v-col>
            </v-row>

            <v-divider></v-divider>
            <v-row>
              <v-col cols="4" class="txtreg txtInfo">Weight</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7">{{ pet_data.weight }} Kg</v-col>
            </v-row>

            <v-divider v-if="status === 0"></v-divider>
            <v-row v-if="status === 0">
              <v-col cols="4" class="txtreg txtInfo">Last seen</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7">{{ lost_data.datetime.split("T")[0] }}</v-col>
            </v-row>

            <v-divider v-if="status === 0"></v-divider>
            <v-row v-if="status === 0">
              <v-col cols="4" class="txtreg txtInfo">Location</v-col>
              <v-col cols="1" class="txtsmall">:</v-col>
              <v-col cols="7" v-if="lost_data.lastseenplace !== null">{{
                lost_data.lastseenplace
              }}</v-col>
              <v-col cols="7" v-if="lost_data.lastseenplace === null">{{
                "-"
              }}</v-col>
            </v-row>
          </v-container>
        </v-card>
        <GmapMap
          v-if="status === 0"
          ref="map"
          id="map"
          :center="currentPositions"
          :zoom="15"
          map-type-id="roadmap"
          :options="{
            minZoom: 8,
            zoomControl: false,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            rotateControl: false,
            fullscreenControl: false,
            disableDefaultUI: true,
            clickableIcons: false
          }"
          style="width: 100%;height: 200px;"
          @click="getclickLocation"
        >
          <GmapMarker :position="currentPositions" />
        </GmapMap>
        <v-card v-if="status === 0" elevation="0">
          <v-container>
            <v-row>
              <v-col>
                <v-btn
                  elevation="0"
                  class="white--text"
                  block
                  color="shop"
                  @click="openlostDialog"
                  >Found Pet</v-btn
                >
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-card>
    </v-app>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  components: {},
  //   updated: function () {
  //     this.$nextTick(function () {
  //       if (this.$store.state.loading === true) {
  //         this.$store.commit('getloading', false)
  //       }
  //     })
  //   },
  data () {
    return {
      status: 0,
      resultQR: '',
      pet_data: '',
      lost_data: null,
      currentPositions: { lat: 0, lng: 0 }
    }
  },
  created () {
    this.resultQR = this.$route.params.token
  },
  async mounted () {
    await this.Showprofile()
    if (this.$store.state.loading === true) {
      this.$store.commit('getloading', false)
    }
  },
  methods: {
    async Showprofile () {
      const body = {
        hw_qrcode: this.resultQR
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'show_Profile',
        body
      )
      console.log(result)
      this.pet_data = result.data.data[0]

      this.getLostdata()
    },
    async getLostdata () {
      const body = {
        hw_qrcode: this.resultQR
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_fromlost',
        body
      )
      result.data.list_pet_fromlost.forEach(petlost => {
        if (petlost.pet_id === this.pet_data.pet_id) {
          this.lost_data = petlost
        }
      })
      console.log(this.lost_data)
      this.currentPositions.lat = this.lost_data.lastseen_latitude
      this.currentPositions.lng = this.lost_data.lastseen_longitude
    },
    setmarker () {}
  }
}
</script>

<style scoped>
.txtInfo {
  font-size: 14px;
}
</style>
