<template>
  <div>
    <v-app>
      <v-main>
        <loading></loading>
        <router-view></router-view>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import loading from '@/components/Loading'
export default {
  name: 'App',
  components: {
    loading
  },
  data () {
    return {}
  },
  methods: {},
  created () {},
  computed: {
    loading () {
      return this.$store.state.loading
    }
  },
  watch: {
    loading (newValue) {
      this.$nextTick(() => {
        if (newValue === false) {
          this.$store.commit('getloading', false)
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import "./assets/style/main.css";
</style>
