<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >

      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Search User</v-toolbar-title>

        </v-toolbar>
        <v-list
          three-line
          subheader
        >
<v-container  class="pa-0">
     <div>
    <TabsMenu :active="tab" @select="selectTab"/>
    <Username v-if="tab === 0"/>
    <OneEmail v-if="tab === 1"/>
    <MyQRCode v-if="tab === 2"/>

  </div>
    </v-container>
        </v-list>

      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import Username from '@/components/Searchuser/Username'
import OneEmail from '@/components/Searchuser/OneEmail'
import MyQRCode from '@/components/Searchuser/MyQRCode'
import TabsMenu from '@/components/Searchuser/TabsMenu'
export default {
  components: {
    Username,
    OneEmail,
    MyQRCode,
    TabsMenu
  },
  created () {
    this.tab = parseInt(this.$route.params.tab)
  },

  data () {
    return {
      tab: 0,
      pathS3: process.env.VUE_APP_S3,
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    selectTab (key) {
      this.tab = key
    },
    openDialog (info) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
    },
    closeDialog () {
      this.dialog = false
    }
  }
}
</script>
