<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="closeDialog()">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Search User</v-toolbar-title>
        </v-toolbar>
        <v-list three-line subheader>
          <v-container class="pa-0 mt-n4">
            <v-row class="py-0">
              <v-col align="center" cols="12">
                <v-card tile elevation="0" class="pa-0" v-if="whenshow === 'Scan'" height="850" color="#333333">
                  <QRScaner v-on:QRcode="getQRscan" v-bind:searchUser="true"/>
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-list>
      </v-card>
    </v-dialog>
    <FriendListdialog ref="FriendListdialog" />
  </v-row>
</template>

<script>
import FriendListdialog from '@/components/Timeline/FriendListdialog'
import QRScaner from '@/components/QRScaner'

export default {
  components: {
    QRScaner,
    FriendListdialog
  },
  data () {
    return {
      searchuserDATA: [],
      dialog: false,
      whenshow: 'Scan',
      resultScan: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    openDialog (info) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.whenshow = 'Scan'
      //   console.log('info', info)
      //   this.info = info
    },
    closeDialog () {
      this.dialog = false
      this.whenshow = ''
    },
    async getQRscan (event) {
      console.log(event)
      this.closeDialog()
      this.resultScan = event
      this.$emit('resultScan', this.resultScan)
      // this.openFriendListdialog(this.searchuserDATA)
    },
    openFriendListdialog (info) {
      this.$refs.FriendListdialog.openDialog(info)
    }
  }
}
</script>
