<template>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary" elevation="0">
          <v-btn icon dark @click="closeDialog()">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Comments</v-toolbar-title>
        </v-toolbar>
        <v-card
          tile
          class=" mx-auto elevation-0 mt-2"
          :class="$vuetify.breakpoint.xs ? '' : 'px-3'"
          width="100%"
        >
          <v-card elevation="0">
            <v-card-actions>
              <v-avatar size="25px">
                <v-img :src="info.owner_pic"></v-img>
              </v-avatar>
              <div class="body-1 ml-2">{{ info.owner_name }}</div>
              <v-spacer></v-spacer>
              <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon color="greyC" v-bind="attrs" v-on="on"
                    >mdi-dots-horizontal</v-icon
                  >
                </template>
                <v-list>
                  <v-list-item v-for="(item1, index) in menu" :key="index">
                    <v-list-item-title
                      v-if="item1.todo === 'edit'"
                      class="greyB--text"
                      @click="openAddstory(info, 'editpost')"
                      >{{ item1.title }}</v-list-item-title
                    >
                    <v-list-item-title
                      v-if="item1.todo === 'delete'"
                      class="greyB--text"
                      @click="openDeletestory(info)"
                      >{{ item1.title }}</v-list-item-title
                    >
                  </v-list-item>
                </v-list>
              </v-menu>
            </v-card-actions>

            <v-img :src="info.picture" height="100%" width="100%"></v-img>
            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-row>
            </template>

            <v-card-text
              class="text--primary "
              :class="$vuetify.breakpoint.xs ? '' : 'px-1'"
            >
              <v-btn
                v-if="info.like_status === 0"
                @click="likepost(info.post_id)"
                class="ml-n3"
                icon
              >
                <v-icon>mdi-heart-outline</v-icon>
              </v-btn>
              <v-btn
                v-if="info.like_status === 1"
                @click="unlikepost(info.post_id)"
                class="ml-n3"
                icon
              >
                <v-icon color="red">mdi-heart</v-icon>
              </v-btn>
              <span>{{ info.count_like }} like</span>
              <v-btn icon>
                <v-icon>mdi-message-outline</v-icon>
              </v-btn>
              <span>{{ info.count_comment }} Comments </span>
              <read-more
                class="readmore"
                more-str="read more"
                :text="info.caption"
                link="javascript: void(0)"
                less-str="read less"
                :max-chars="50"
              ></read-more>
              <div class="mt-1 grey--text txtsmall">
                <v-icon small class="collocateIcon">mdi-clock-outline</v-icon>
                <span class="ml-1">{{ info.create_date }}</span>
              </div>
            </v-card-text>
          </v-card>

          <v-data-iterator
            :items="commentData"
            :items-per-page="itemsPerPage"
            :page="page"
            hide-default-footer
            data-app
            loading
            loading-text=""
          >
            <template v-slot:default="props">
              <v-row
                class="px-0 mx-0"
                cols="12"
                v-for="(item, index) in props.items"
                :key="index"
              >
                <v-col cols="1" class="mx-0">
                  <v-avatar
                    v-if="
                      item.user_picture !== null && item.user_picture !== ''
                    "
                    class="mt-n2"
                    size="30px"
                  >
                    <v-img :src="item.owner_pic"></v-img>
                  </v-avatar>
                  <v-avatar
                    v-if="
                      item.user_picture === null && item.user_picture === ''
                    "
                    class="mt-n2"
                    size="30px"
                  >
                    <v-img src="../../assets/icon/icon_PETDY.png"></v-img>
                  </v-avatar>
                </v-col>

                <v-col class="pl-3 mx-0" align="" cols="4">
                  <read-more
                    class="primary--text ml-2"
                    more-str=""
                    :text="item.owner_name"
                    link="javascript: void(0)"
                    less-str=""
                    :max-chars="10"
                  ></read-more>
                </v-col>

                <v-col cols="6" class="px-0 mx-0">
                  {{ item.comment }}
                </v-col>

                <v-col cols="1" align="center" class="px-0 mx-0">
                  <v-menu v-if="item.comment_by_id === userid" offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon color="greyC" v-bind="attrs" v-on="on"
                        >mdi-dots-horizontal</v-icon
                      >
                    </template>
                    <v-list>
                      <v-list-item
                        v-for="(item1, index) in menucomments"
                        :key="index"
                      >
                        <v-list-item-title
                          v-if="item1.eventclick === 'Edit'"
                          class="greyB--text"
                          @click="openTimelineEditComments(item)"
                          >{{ item1.title }}</v-list-item-title
                        >
                        <v-list-item-title
                          v-if="item1.eventclick === 'Delete'"
                          class="greyB--text"
                          @click="openTimelineDelete(item)"
                          >{{ item1.title }}</v-list-item-title
                        >
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </v-col>
              </v-row>
            </template>
          </v-data-iterator>
        </v-card>

        <v-card class="mt-16">
          <v-row>
            <div class="ml-n0" style="position: fixed;  bottom: 70px;">
              <v-toolbar
                elevation="0"
                height="100"
                style="position: fixed; height:550px;  width:1000px;"
              >
                <v-avatar class="mt-n7" size="45px">
                  <v-img :src="info.user_picture"></v-img>
                </v-avatar>
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-text-field
                    v-model="data.comment"
                    :rules="TopicRules"
                    class="ml-3"
                    label="message"
                    outlined
                    filled
                    rounded
                    dense
                  ></v-text-field>
                </v-form>
                <v-btn
                  @click="addcomment()"
                  :disabled="!valid"
                  x-large
                  icon
                  color="primary"
                  class="mt-n7"
                >
                  <v-icon>mdi-send</v-icon>
                </v-btn>
              </v-toolbar>
            </div>
          </v-row>
        </v-card>
      </v-card>
      <Addstory ref="Addstory" v-on:edit="editpost" />
      <TimelineEditComments ref="TimelineEditComments" />
      <TimelineDelete ref="TimelineDelete" />
      <Deletepopup ref="Deletepopup" v-on:delete="deletepost"></Deletepopup>
    </v-dialog>
</template>

<script>
import axios from 'axios'
import Addstory from '@/components/Home/Mystory/Addstory'
import TimelineEditComments from '@/components/Timeline/TimelineEditComments'
import TimelineDelete from '@/components/Timeline/TimelineDelete'
import Deletepopup from '@/components/DeletePopup'
// import { mapState } from 'vuex'

export default {
  components: {
    Addstory,
    TimelineEditComments,
    TimelineDelete,
    Deletepopup
  },
  data () {
    return {
      commentData: [],
      postData: [],
      valid: true,
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: '',
      idpost: '',
      userid: '',
      page: 1,
      data: {
        user_id: '',
        comment: '',
        post_id: null
      },
      TopicRules: [v => !!v || ''],
      itemsPerPage: 99999,
      menu: [
        {
          title: 'Edit Post',
          todo: 'edit'
        },
        {
          title: 'Delete Post',
          todo: 'delete'
        }
      ],
      menucomments: [
        {
          title: 'Edit',
          eventclick: 'Edit'
        },
        {
          title: 'Delete',
          eventclick: 'Delete'
        }
      ]
    }
  },
  computed: {
    updatePost () {
      return this.$store.state.updatePost
    },
    updateTimelineDialog () {
      return this.$store.state.updateTimelineDialog
    },
    updatecomment () {
      return this.$store.state.updatecomment
    }
  },
  watch: {
    async updatecomment (newval) {
      this.$nextTick(async () => {
        if (newval === true) {
          await this.updateInfo()
          await this.getpost()
          await this.getcomment()
          this.$store.commit('getupdatecomment', false)
        }
      })
    },
    async updatePost (newValue) {
      if (newValue === true) {
        await this.updateInfo()
        this.$store.commit('getupdatePost', false)
      }
    },
    async updateTimelineDialog (newValue) {
      if (newValue === true) {
        await this.getpost()
        await this.getcomment()
        this.$store.commit('getupdateTimelineDialogFALSE')
      }
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async likepost () {
      await this.axios
        .post(process.env.VUE_APP_API + 'like_post', {
          user_id: this.userid,
          post_id: this.info.post_id
        })
        .then(res => {
          console.log(res.data)
        })
      this.getpost()
      this.$store.commit('getupdatePost', true)
    },
    async unlikepost () {
      await this.axios
        .post(process.env.VUE_APP_API + 'unlike_post', {
          user_id: this.userid,
          post_id: this.info.post_id
        })
        .then(res => {
          console.log(res.data)
        })
      this.getpost()
      this.$store.commit('getupdatePost', true)
    },
    async addcomment () {
      if (this.$refs.form.validate() === true) {
        this.data.user_id = this.userid
        this.data.post_id = this.info.post_id
        await this.axios
          .post(process.env.VUE_APP_API + 'add_comment', this.data)
          .then(res => {
            console.log(res.data)
          })
        this.$refs.form.reset()
        this.updateInfo()
        this.getpost()
        this.getcomment()
        this.$store.commit('getupdatePost', true)
      } else {
        console.log('can not create addcomment')
      }
    },
    async updateInfo () {
      const body = {
        user_id: this.userid,
        friend_id: this.info.user_id || this.info.user_create_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      result.data.list_post.forEach(post => {
        if (post.post_id === this.info.post_id) {
          this.$nextTick(() => {
            this.info = post
            console.log(this.info)
          })
        }
      })
    },
    async getpost () {
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      this.friendid = this.info.user_id || this.info.user_create_id
      const body = {
        user_id: this.userid,
        friend_id: this.info.user_id || this.info.user_create_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      this.postData = result.data.list_post
    },
    async getcomment () {
      const body = {
        post_id: this.info.post_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_comment_by_post_id',
        body
      )
      this.commentData = result.data.list_comment
    },
    openDialog (info) {
      console.log(info)
      this.$store.commit('getloading', true)
      this.dialog = true
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id

      this.info = info
      this.getcomment()
      this.getpost()
      this.$refs.form.reset()

      this.data.user_id = this.userid
      this.data.comment = null
      this.data.post_id = this.info.post_id
    },
    closeDialog () {
      this.$store.commit('getupdateTimeline', true)
      this.dialog = false
      // this.$store.commit('getupdateTimeline')
    },
    openAddstory (info, todo) {
      this.$refs.Addstory.openDialog(info, todo)
    },
    openTimelineEditComments (info) {
      this.$refs.TimelineEditComments.openDialog(info)
    },
    openTimelineDelete (info) {
      // this.dialog = false
      this.$refs.TimelineDelete.openSheet(info)
    },
    openDeletestory (info) {
      this.$refs.Deletepopup.openSheet(info, 'deletepost')
    },
    deletepost () {
      this.dialog = false
      this.$emit('updatepost')
    },
    async editpost (event) {
      const body = {
        user_id: event.user_id,
        friend_id: event.user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      console.log(result)
      await result.data.list_post.forEach(post => {
        if (post.post_id === event.post_id) {
          this.$nextTick(() => {
            this.info = post
          })
        }
      })
    }
  },

  mounted () {
    this.getcomment()
    this.idpost = this.info.post_id
  }
}
</script>

<style>
a:visited {
  color: #5cbe66 !important;
}
a:link {
  text-decoration: none !important;
}
</style>
