<template>
  <v-dialog v-model="dialog" width="800" height="100" persistent>
    <v-card width="800">
      <v-toolbar class="toolbar1" dark color="primary">
        <v-toolbar-title>Edit Comment</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <v-card tile elevation="0" height="150">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-col cols="12" sm="6" md="4">
            <v-textarea
              v-model="data.comment"
              label="comment"
              :rules="TopicRules"
              auto-grow
              outlined
              rows="1"
              row-height="125"
              dense
            ></v-textarea>
          </v-col>
        </v-form>
      </v-card>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="closeDialog()">CLOSE</v-btn>
        <v-btn text color="success" @click="editcomment()">SAVE</v-btn>
      </v-card-actions>
    </v-card>
    <loading/>
  </v-dialog>
</template>

<script>
import loading from '@/components/Loading'
export default {
  components: {
    loading
  },
  data () {
    return {
      pathS3: process.env.VUE_APP_S3,
      valid: true,
      dialog: false,
      info: '',
      data: {
        user_id: '',
        comment: '',
        comment_id: ''
      },
      TopicRules: [v => !!v || '']
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async editcomment () {
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        console.log('addcomment')
        await this.axios
          .post(process.env.VUE_APP_API + 'edit_comment', this.data)
          .then(res => {
            console.log(res.data)
          })
        this.$store.commit('getupdateTimelineDialog')
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      } else {
        console.log('can not create addcomment')
      }
    },
    closeDialog () {
      this.dialog = false
      this.$store.commit('getupdateTimelineDialog', true)
    },
    openDialog (info) {
      this.$store.commit('getloading', true)
      console.log()
      this.dialog = true
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      this.info = info
      this.data.user_id = this.userid
      this.data.comment = this.info.comment
      this.data.comment_id = this.info.comment_id
    }
  }
}
</script>

<style scoped></style>
