<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary" elevation="0">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>{{ info.owner_name }}</v-toolbar-title>
        </v-toolbar>
        <v-container class="py-0">
          <v-list three-line subheader>
            <v-card
              height=""
              tile
              class=" mx-auto elevation-0 mt-2"
              :class="$vuetify.breakpoint.xs ? '' : 'px-3'"
              width="100%"
            >
              <v-row>
                <v-col class="pr-1" cols="4" sm="3" md="2">
                  <v-avatar size="100">
                    <v-img :src="info.owner_pic"></v-img>
                  </v-avatar>
                </v-col>
                <v-col class="pl-0" cols="8" sm="9" md="10">
                  <v-row class="mt-3 pl-5">
                    <v-col cols="7">
                      <!-- <b class="secondary--text">{{ info.owner_name }}</b> -->
                      <read-more
                        class="secondary--text txtbold"
                        style="font-size: 16px;"
                        more-str=""
                        :text="info.owner_name"
                        :max-chars="15"
                      ></read-more>
                    </v-col>
                    <v-col>
                      <v-row class="mt-3 " align="center">
                        <v-btn
                          v-if="this.followstatus === 1"
                          @click="unfollow()"
                          elevation="0"
                          rounded
                          height="27"
                          width="100"
                          class="primary mt-n4 ml-n1"
                        >
                          <span class="txtsmall white--text">Following</span>
                        </v-btn>
                        <v-btn
                          v-if="this.followstatus === 0"
                          @click="follow()"
                          elevation="0"
                          rounded
                          height="27"
                          width="100"
                          class=" primary--stoke mt-n4 ml-n1"
                        >
                          <span class="txtsmall primary--text ">Follow</span>
                        </v-btn>
                      </v-row>
                    </v-col>
                  </v-row>
                  <v-row class="mt-n5">
                    <v-col class="pb-0 text-center" cols="4">
                      <h2 class="txtbold">{{ numOfPost }}</h2>
                      <div class="txtsmall">Posts</div>
                    </v-col>
                    <v-col class="pb-0 pl-0 text-center" cols="4">
                      <h2 class="txtbold">{{ numOfFollowing }}</h2>
                      <div class="txtsmall">Following</div>
                    </v-col>
                    <v-col class="pb-0 pl-0 text-center" cols="4">
                      <h2 class="txtbold">{{ numOfFollower }}</h2>
                      <div class="txtsmall">Followers</div>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-card>
          </v-list>
        </v-container>
        <!-- <v-divider /><v-divider /><v-divider /><v-divider /> -->
        <v-container class="pt-0 greyF1">
          <v-row>
            <v-col cols="12" class="pa-0 ma-0">
              <v-card
                v-if="nopost === true"
                class="mt-2"
                elevation="0"
                height="700"
              >
                <v-row justify="center" class="mx-0">
                  <v-col>
                    <v-row justify="center" class="mt-16 mx-0">
                      <v-card height="150" width="150" elevation="0">
                        <v-img src="../../assets/icon/Device/bone.png"> </v-img>
                      </v-card>
                    </v-row>
                  </v-col>
                </v-row>

                <v-row justify="center" class="mt-2 mx-0">
                  <v-col cols="6">
                    <v-row justify="center">
                      <div
                        class="txtbold"
                        style="font-size:25px; color: #888888;"
                      >
                        No post yet
                      </div>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
          <v-data-iterator
            :items="postData"
            :items-per-page="itemsPerPage"
            :page="page"
            hide-default-footer
            data-app
            loading
            loading-text=""
            v-if="nopost === false"
          >
            <template v-slot:default="props">
              <v-row class=" ">
                <v-col
                  class="pa-0"
                  col="12"
                  sm="6"
                  md="4"
                  v-for="(item, index) in props.items"
                  :key="index"
                >
                  <v-card
                    height="535"
                    min-width="250"
                    tile
                    class=" mx-auto elevation-0 mt-2"
                    :class="$vuetify.breakpoint.xs ? '' : 'px-3'"
                    width="100%"
                  >
                    <v-card-actions
                      :class="$vuetify.breakpoint.xs ? 'pl-3' : 'pl-0'"
                    >
                      <v-avatar size="25px">
                        <!-- <v-img :src="item.user_picture"></v-img> -->
                        <v-img
                          v-if="item.owner_pic !== ''"
                          :src="item.owner_pic"
                        ></v-img>
                        <v-img v-else src="../../assets/icon/icon_PETDY.png">
                        </v-img>
                      </v-avatar>
                      <div class="body-1 ml-2">{{ item.owner_name }}</div>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                    <div @click="openTimelinedialog(item)">
                      <v-img
                        height="367"
                        v-if="item.picture !== ''"
                        :src="item.picture"
                      ></v-img>
                      <v-img
                        height="367"
                        v-else
                        src="../../assets/icon/icon_PETDY.png"
                      >
                      </v-img>
                    </div>
                    <v-card-text
                      class="text--primary"
                      :class="$vuetify.breakpoint.xs ? '' : 'px-1'"
                    >
                      <v-btn
                        v-if="item.like_status === 0"
                        @click="likepost(item.post_id)"
                        class="ml-n3"
                        icon
                      >
                        <v-icon>mdi-heart-outline</v-icon>
                      </v-btn>
                      <v-btn
                        v-if="item.like_status === 1"
                        @click="unlikepost(item.post_id)"
                        class="ml-n3"
                        icon
                      >
                        <v-icon color="red">mdi-heart</v-icon>
                      </v-btn>
                      <span>{{ item.count_like }} like </span>
                      <v-btn icon>
                        <v-icon>mdi-message-outline</v-icon>
                      </v-btn>
                      <span>{{ item.count_comment }} Comments </span>
                      <div>{{ item.caption }}</div>
                      <div class="mt-1 grey--text txtsmall">
                        <v-icon small class="collocateIcon"
                          >mdi-clock-outline</v-icon
                        >
                        <span class="ml-1">{{ item.create_date }}</span>
                      </div>
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
            </template>
          </v-data-iterator>
        </v-container>
      </v-card>
    </v-dialog>
    <Timelinedialog ref="Timelinedialog" />
  </v-row>
</template>

<script>
import axios from 'axios'
import Timelinedialog from '@/components/Timeline/Timelinedialog'

export default {
  components: {
    Timelinedialog
  },
  data () {
    return {
      postData: [],
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: '',
      idpost: '',
      page: 1,
      itemsPerPage: 99999,
      userid: '',
      friendid: '',
      numOfPost: null,
      numOfFollowing: null,
      numOfFollower: null,
      followstatus: null,
      profileName: '',
      nopost: false
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async likepost (postID) {
      await this.axios.post(process.env.VUE_APP_API + 'like_post', {
        user_id: this.userid,
        post_id: postID
      })
      this.getpost()
    },
    async unlikepost (postID) {
      await this.axios.post(process.env.VUE_APP_API + 'unlike_post', {
        user_id: this.userid,
        post_id: postID
      })
      this.getpost()
    },
    async unfollow () {
      await this.axios.post(process.env.VUE_APP_API + 'unfollow_user', {
        followby_id: this.userid,
        followto_id: this.friendid
      })
      this.getFollow()
      this.$store.commit('getupdateFriendList')
    },
    async follow () {
      await this.axios.post(process.env.VUE_APP_API + 'follow_user', {
        followby_id: this.userid,
        followto_id: this.friendid
      })
      this.getFollow()
      this.$store.commit('getupdateFriendList')
    },

    async getFollow () {
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      this.friendid = this.info.user_id
      const body = {
        user_id: this.userid,
        friend_id: this.friendid
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_user_follow',
        body
      )
      console.log(result)
      this.numOfFollowing = result.data.following_count
      this.numOfFollower = result.data.follower_count
      this.numOfPost = result.data.post_count
      this.followstatus = result.data.follow_status
    },
    async getpost () {
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      this.friendid = this.info.user_id || this.info.user_create_id
      const body = {
        user_id: this.userid,
        friend_id: this.friendid
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      if (result.data.list_post.length === 0) {
        console.log('nopost')
        this.nopost = true
      } else {
        this.nopost = false
        this.postData = result.data.list_post
        result.data.list_post.forEach(post => {
          var result = post.create_date.split('T')
          var result2 = result[1].split('.')
          post.DAY = result[0]
          post.TIME = result2[0]
        })
      }
    },
    openTimelinedialog (info) {
      this.$refs.Timelinedialog.openDialog(info)
    },
    async openDialog (info) {
      this.$store.commit('getloading', true)
      console.log(info)
      this.dialog = true
      this.info = info
      this.getpost()
      this.getFollow()
      this.profileName =
        this.info.first_name_en + ' ' + ' ' + this.info.last_name_en
    },
    closeDialog () {
      this.$store.commit('getupdateTimeline')
      this.dialog = false
    }
  },
  mounted () {
    this.getpost()
    this.getFollow()
  },
  computed: {
    updatethisPost () {
      return this.$store.state.updatePost
    }
  },
  watch: {
    async updatethisPost (newValue) {
      if (newValue === true) {
        await this.getpost()
        this.$store.commit('getupdatePost', false)
      }
    }
  }
}
</script>
