<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Pet Infomation</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <v-card tile elevation="0" color="greyF1">
          <v-card tile elevation="0">
            <v-row class="mx-0">
              <v-col class="pl-5" cols="4" sm="3" md="2">
                <v-avatar size="130" rounded="circle">
                  <v-img :src="info.pic"></v-img>
                </v-avatar>
              </v-col>
              <v-col class="pl-10 pt-5" cols="7" sm="9" md="10">
                <v-row class="mt-3 pl-4 pr-5" align="center">
                  <b class="primary--text">{{ info.name }}</b>
                  <v-spacer></v-spacer>
                </v-row>

                <v-row class="mt-3 pl-4" align="center">
                  <v-col class="ma-0 pa-0">
                    <b
                      class="greyC--text txtInfo"
                      v-if="info.pet_detail === null"
                      >ยินดีที่ได้รู้จัก ขอต้อนรับเข้าสู่หน้าแสดงข้อมูลของฉัน</b
                    >
                    <!-- <b class="greyC--text txtInfo" v-if="info.pet_detail !== null"
                      >{{info.pet_detail}}</b
                    > -->
                    <read-more
                      v-if="info.pet_detail !== null"
                      class="readmore greyC--text txtInfo"
                      more-str="read more"
                      :text="info.pet_detail"
                      less-str="read less"
                      :max-chars="20"
                    ></read-more>
                  </v-col>
                </v-row>

                <v-row class="mt-3 pl-0" align="center">
                  <v-chip
                    v-if="info.status === 1"
                    color="hospital"
                    text-color="white"
                    class="ml-4 px-5"
                    >Normal</v-chip
                  >

                  <v-chip
                    v-if="info.status === 0"
                    color="shop"
                    text-color="white"
                    class="ml-4 px-5"
                    >Lost !!!</v-chip
                  >
                </v-row>
              </v-col>

              <v-col cols="1" class="pt-5">
                <v-row class="mt-3">
                  <v-menu
                    offset-y
                    v-if="
                      info.user_create_id === this.$store.state.userID ||
                        info.user_id === this.$store.state.userID
                    "
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon color="greyC" v-bind="attrs" v-on="on" class
                        >mdi-dots-horizontal</v-icon
                      >
                    </template>
                    <v-list>
                      <v-list-item v-for="(item, index) in menu" :key="index">
                        <v-list-item-title
                          v-if="item.todo === 'editdisplay'"
                          class="greyB--text"
                          @click="openeditdisplay(info)"
                          >{{ item.title }}</v-list-item-title
                        >
                        <v-list-item-title
                          v-if="item.todo === 'editstatus'"
                          class="greyB--text"
                          @click="openEditPetDialog(info, 'editpet')"
                          >{{ item.title }}</v-list-item-title
                        >
                        <v-list-item-title
                          v-if="item.todo === 'deletepet'"
                          class="greyB--text"
                          @click="openDeletepopup(info)"
                          >{{ item.title }}</v-list-item-title
                        >
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </v-row>
              </v-col>
            </v-row>
          </v-card>
          <v-card tile elevation="0" class="mt-1 pa-2">
            <v-container class="py-0">
              <v-row>
                <v-col cols="12" class="txtreg greyB--text">INFORMATION</v-col>
              </v-row>

              <v-row>
                <v-col cols="4" class="txtreg txtInfo">Breed</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ info.breed }}</v-col>
              </v-row>

              <v-divider></v-divider>
              <v-row>
                <v-col cols="4" class="txtreg txtInfo">Gender</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ info.gender }}</v-col>
              </v-row>

              <v-divider></v-divider>
              <v-row>
                <v-col cols="4" class="txtreg txtInfo">Birth Date</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7" v-if="from === 'frompetlocation'">
                  {{ info.date_of_birth.split("T")[0] }}
                </v-col>
                <v-col cols="7" v-if="from === 'fromother'">
                  {{ info.birthdate.split("T")[0] }}
                </v-col>
              </v-row>
              <v-divider></v-divider>
              <v-row>
                <v-col cols="4" class="txtreg txtInfo">Fur Color</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ info.furcolor }}</v-col>
              </v-row>

              <v-divider></v-divider>
              <v-row>
                <v-col cols="4" class="txtreg txtInfo">Weight</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ info.weight }} Kg</v-col>
              </v-row>

              <v-divider></v-divider>
              <v-row class="mt-3" v-if="info.status === 0">
                <v-col cols="12" class="txtreg greyB--text">LOST DETAIL</v-col>
              </v-row>
              <v-row v-if="info.status === 0">
                <v-col cols="4" class="txtreg txtInfo">Detail</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ lostdetail.title }}</v-col>
              </v-row>

              <v-divider v-if="info.status === 0"></v-divider>
              <v-row v-if="info.status === 0">
                <v-col cols="4" class="txtreg txtInfo">Last seen</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ lostdetail.datetime.split("T")[0] }}</v-col>
              </v-row>

              <v-divider v-if="info.status === 0"></v-divider>
              <v-row v-if="info.status === 0">
                <v-col cols="4" class="txtreg txtInfo"> Last seen location</v-col>
                <v-col cols="1" class="txtsmall">:</v-col>
                <v-col cols="7">{{ lostdetail.lastseenplace }}</v-col>
              </v-row>
            </v-container>
          </v-card>
          <GmapMap
            v-if="info.status === 0"
            ref="map"
            id="map"
            :center="currentPositions"
            :zoom="15"
            map-type-id="roadmap"
            :options="{
              zoomControl: false,
              mapTypeControl: false,
              scaleControl: false,
              streetViewControl: false,
              rotateControl: false,
              fullscreenControl: false,
              disableDefaultUI: true,
              clickableIcons: false
            }"
            style="width: 100%;height: 200px;"
            @click="getclickLocation"
          >
            <GmapMarker :position="currentPositions" />
          </GmapMap>
          <v-card v-if="info.status === 0" elevation="0">
            <v-container>
              <v-row>
                <v-col>
                  <v-btn
                    elevation="0"
                    class="white--text"
                    block
                    color="shop"
                    @click="openlostDialog"
                    >Found Pet</v-btn
                  >
                </v-col>
              </v-row>
            </v-container>
          </v-card>
        </v-card>
      </v-card>

      <v-dialog
        v-model="lostdialog"
        hide-overlay
        transition="dialog-bottom-transition"
      >
        <v-card class="text-center" elevation="0">
          <v-toolbar dark color="primary" elevation="0">
            <v-toolbar-title>Found Pet</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-container class="pa-3">
            <div class="txttitle pt-3">
              If you found my pet, please contact
            </div>

            <div class="txtbold secondary--text txtTel pt-3">
              {{ lostdetail.contact }}
            </div>

            <div class="txttitle pt-1">
              or
            </div>
            <v-row justify="center">
              <v-col cols="8">
                <v-row>
                  <v-btn
                    block
                    class="mt-3 txtbold"
                    outlined
                    color="secondary"
                    @click="openchat"
                    >chat</v-btn
                  >
                </v-row>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-dialog>
      <EditDisplayText ref="EditDisplayText" v-on:updatedisplay="updateinfo" />
      <AddpetForm ref="AddpetFormDialog" v-on:updateinfo="updateinfo" />
      <Deletepopup
        ref="Deletepopup"
        v-on:updateitempet="closeDialog"
      ></Deletepopup>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from 'axios'
import EditDisplayText from '@/components/Home/Mypet/Addpet/EditDisplayText'
import AddpetForm from '@/components/Home/Mypet/Addpet/AddpetForm'
import Deletepopup from '@/components/DeletePopup'

export default {
  components: {
    EditDisplayText,
    AddpetForm,
    Deletepopup
  },
  data () {
    return {
      currentPositions: { lat: 0, lng: 0 },
      lostdialog: false,
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: '',
      menu: [
        {
          title: 'Edit Display Text',
          todo: 'editdisplay'
        },
        {
          title: 'Edit Pet',
          todo: 'editstatus'
        },
        {
          title: 'Delete Pet',
          todo: 'deletepet'
        }
      ],
      from: '',
      lostdetail: ''
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async openchat () {
      const body = {
        one_id: this.lostdetail.oneid
      }
      const headers = {
        Authorization:
          'Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1'
      }
      const result = await axios.post(
        'https://chat-api.one.th/manage/get_link_addfriend',
        body,
        { headers }
      )
      window.open(result.data.link)
    },
    openlostDialog () {
      this.lostdialog = true
    },
    async updateinfo (event) {
      const body = {
        user_id: JSON.parse(this.$ls.get('userprofile'))[0].user_id,
        friend_id: JSON.parse(this.$ls.get('userprofile'))[0].user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_by_userid',
        body
      )
      console.log('result', result)
      result.data.pet_data.forEach(pet => {
        if (pet.pet_id === event.pet_id) {
          this.info = pet
        }
      })
    },
    openDeletepopup (info) {
      this.$refs.Deletepopup.openSheet(info, 'deletepet')
    },
    openEditPetDialog (info, todo) {
      this.$refs.AddpetFormDialog.openDialog(info, todo)
    },
    async openDialog (info, from) {
      this.$store.commit('getloading', true)
      console.log(info)
      console.log(from)
      if (from === 'frompetlocation') {
        this.info = info
        this.from = from
        this.dialog = true
      } else if (from === 'fromother') {
        this.info = info
        this.from = from
        this.dialog = true
        await this.getpetlost()
      }
    },
    closeDialog () {
      this.dialog = false
    },
    async getpetlost () {
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_fromlost'
      )
      console.log('result', result)
      result.data.list_pet_fromlost.forEach(lost => {
        if (lost.pet_id === this.info.pet_id) {
          this.lostdetail = lost
          this.currentPositions.lat = lost.lastseen_latitude
          this.currentPositions.lng = lost.lastseen_longitude
        }
      })
    },
    openeditdisplay () {
      this.$refs.EditDisplayText.openDialog(this.info, 'editdisplaytext')
    }
  },
  computed: {
    updatepage () {
      return this.$store.state.updateMypet
    }
  },
  watch: {
    updatepage (newValue) {
      if (newValue === true) {
        this.updateinfo(this.$store.state.pet_info)
        this.$store.commit('getupdateMypet', false)
      }
    }
  }
}
</script>

<style scoped>
.txtInfo {
  font-size: 14px;
  font-weight: bold;
}
.txttitle {
  font-size: 15px;
}
.txtTel {
  font-size: 30px;
}
</style>
