<template>
  <div>
    <v-container v-if="whenshow === 'beforescan'">
      <v-row>
        <v-col align="center" cols="12">
          <vue-qrcode width="300" :value="value" />
          <!-- <qrcode-vue  :size="size" level="H" /> -->
        </v-col>
        <v-col class="mt-n7" align="center" cols="12">
          <span> My QR Code</span>
        </v-col>
      </v-row>
      <v-col class="mt-15">
        <v-btn
          @click="openScan()"
          elevation="0"
          height="35"
          block
          class="primary white--text"
        >
          Scan QR Code
        </v-btn>
      </v-col>
    </v-container>

    <v-card
      v-if="whenshow === 'afterscan'"
      elevation="0"
      class="primary--stoke mx-3 my-7"
    >
      <v-container class="pa-0">
        <v-row>
          <v-col align="center" class="" cols="4" sm="3" md="2">
            <v-avatar size="80">
              <img :src="userdata.owner_pic" />
            </v-avatar>
          </v-col>
          <v-col class="" cols="8" sm="9" md="10">
            <v-row cols="12">
              <h4 class="primary--text">
                {{ userdata.first_name_en }} {{ }} {{ userdata.last_name_en }}
              </h4>
            </v-row>
            <v-row class="mt-2" cols="12">
              <h6 class="greyB--text" v-if="userdata.gender === 'M'">
                {{ "Male" }} |
              </h6>
              <h6 class="greyB--text" v-else>{{ "Female" }} |</h6>
              <h6 class="greyB--text pl-1">{{ userdata.mobile_no }}</h6>
            </v-row>
            <v-row class="mt-2 ml-1">
              <v-btn
                @click="openFriendListdialog(userdata)"
                rounded
                height="30"
                elevation="0"
                color="Gray"
                class=""
              >
                <v-icon>mdi-account-outline</v-icon>
                <h5>View Profile</h5></v-btn
              >
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <Scan ref="Scan" v-on:resultScan="setwhenshow" />
    <FriendListdialog ref="FriendListdialog" />
  </div>
</template>

<script>
import Scan from '@/components/Searchuser/Scan'
import axios from 'axios'
import VueQrcode from 'vue-qrcode'
import FriendListdialog from '@/components/Timeline/FriendListdialog'
export default {
  components: {
    VueQrcode,
    Scan,
    FriendListdialog
  },
  data () {
    return {
      whenshow: 'beforescan',
      userid: '',
      value: '',
      userdata: ''
    }
  },
  methods: {
    openFriendListdialog (info) {
      this.$refs.FriendListdialog.openDialog(info)
    },
    async searchuser () {
      const body = {
        key_search: this.resultScan,
        key_type: 'user_id'
      }
      console.log(body)
      const result = await axios.post(
        process.env.VUE_APP_API + 'search_user',
        body
      )
      console.log(result)
      this.userdata = result.data.user_data[0]
    },
    async setwhenshow (event) {
      console.log(event)
      this.resultScan = event
      await this.searchuser()

      this.whenshow = 'afterscan'
    },
    openscanner () {
      this.whenshow = 'Scan'
      this.value = this.userid
    },
    openScan (info) {
      this.$refs.Scan.openDialog(info)
    }
  },
  created () {
    this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
    this.value = this.userid.toString()
    console.log(this.userid)
  }
}
</script>

<style></style>
