<template>
  <GmapMap
    ref="map"
    id="map"
    :center="currentPositions"
    :zoom="15"
    map-type-id="roadmap"
    :options="{
      zoomControl: false,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
      disableDefaultUI: true,
      clickableIcons: false
    }"
    style="width: 100%; height: 300px"
    @click="getclickLocation"
  >
    <GmapMarker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :clickable="true"
      :draggable="true"
      @click="center = m.position"
    />
  </GmapMap>
</template>

<script>
export default {
  props: ['info'],
  data () {
    return {
      currentPositions: { lat: 0, lng: 0 },
      markers: [],
      match_lat: null,
      match_lng: null
    }
  },
  methods: {
    async getcurrentPosition () {
      navigator.geolocation.getCurrentPosition(
        position => {
          this.currentPositions.lat = position.coords.latitude
          this.currentPositions.lng = position.coords.longitude
          // this.markers.push({ position: this.currentPositions })
        },
        error => {
          console.log(error.message)
        }
      )
    },
    getclickLocation (event) {
      this.clearMarker()

      this.$store.commit('getLocationlat', event.latLng.lat())
      this.$store.commit('getLocationlng', event.latLng.lng())
      this.markers.push({
        position: { lat: event.latLng.lat(), lng: event.latLng.lng() }
      })
    },
    setInitMarker (lastseenLat, lastseenLng) {
      console.log(lastseenLat)
      console.log(lastseenLng)
      this.currentPositions.lat = lastseenLat
      this.currentPositions.lng = lastseenLng
      this.markers.push({
        position: { lat: lastseenLat, lng: lastseenLng }
      })
    },
    clearMarker () {
      this.markers = []
    }
  },
  async mounted () {
    await this.getcurrentPosition()
    console.log(this.currentPositions)
  },
  computed: {
    updatemarker () {
      return this.$store.state.updatematchmarker
    }
  },
  watch: {
    updatemarker (newValue) {
      this.$nextTick(() => {
        if (newValue === true) {
          this.match_lat = this.$store.state.matchLatLng.lat
          this.match_lng = this.$store.state.matchLatLng.lng
          this.markers = []
          this.markers.push({
            position: { lat: this.match_lat, lng: this.match_lng }
          })
          this.$store.commit('getupdatematchmarker', false)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped></style>
