<template>
  <div>
    <v-container class="pt-0">
      <v-row justify="space-around white">
        <v-col cols="12" class="px-0 mx-0">
          <FriendList @update="updateEvents" />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12" class="pa-0 ma-0">
        <v-card v-if="nopost === true" class="mt-2" elevation="0" height="700">
          <v-row justify="center" class="mx-0">
            <v-col>
              <v-row justify="center" class="mt-16 mx-0">
                <v-card height="150" width="150" elevation="0">
                  <v-img src="../assets/icon/Device/bone.png">
                  </v-img>
                </v-card>
              </v-row>
            </v-col>
          </v-row>

          <v-row justify="center" class="mt-2 mx-0">
            <v-col cols="6">
              <v-row justify="center">
                <div class="txtbold" style="font-size:25px; color: #888888;">
                  No post yet
                </div>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
        </v-col>
      </v-row>

      <v-data-iterator
        :items="postData"
        :items-per-page="itemsPerPage"
        :page="page"
        hide-default-footer
        data-app
        loading
        loading-text=""
        v-if="nopost === false"
      >
        <template v-slot:default="props">
          <v-row class="">
            <v-col
              class="pa-0"
              col="12"
              sm="6"
              md="4"
              v-for="(item, index) in props.items"
              :key="index"
            >
              <v-card
                height="100%"
                min-width="250"
                tile
                class=" mx-auto elevation-0 mt-2"
                :class="$vuetify.breakpoint.xs ? '' : 'px-3'"
                width="100%"
              >
                <v-card-actions
                  :class="$vuetify.breakpoint.xs ? 'pl-3' : 'pl-0'"
                >
                  <v-avatar size="25px">
                    <v-img :src="item.owner_pic"></v-img>
                  </v-avatar>
                  <div class="body-1 ml-2">{{ item.owner_name }}</div>
                  <v-spacer></v-spacer>
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon color="greyC" v-bind="attrs" v-on="on"
                        >mdi-dots-horizontal</v-icon
                      >
                    </template>
                    <v-list>
                      <v-list-item v-for="(item1, index) in menu" :key="index">
                        <v-list-item-title
                          v-if="item1.todo === 'edit'"
                          class="greyB--text"
                          @click="openAddstory(item, 'editpost')"
                          >{{ item1.title }}</v-list-item-title
                        >
                        <v-list-item-title
                          v-if="item1.todo === 'delete'"
                          class="greyB--text"
                          @click="openDeletestory(item)"
                          >{{ item1.title }}</v-list-item-title
                        >
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </v-card-actions>
                <div @click="openTimelinedialog(item)">
                  <v-img
                    class="pa-0"
                    height="367"
                    v-if="item.picture !== ''"
                    :src="item.picture"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="primary"
                        ></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>
                  <v-img
                    class="pa-0"
                    v-if="item.picture === ''"
                    src="../../src/assets/icon/icon_PETDY.png"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="primary"
                        ></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>
                </div>
                <v-card-text
                  class="text--primary "
                  :class="$vuetify.breakpoint.xs ? '' : 'px-1'"
                >
                  <v-btn
                    v-if="item.like_status === 0"
                    @click="likepost(item.post_id)"
                    class="ml-n3"
                    icon
                  >
                    <v-icon>mdi-heart-outline</v-icon>
                  </v-btn>
                  <v-btn
                    v-if="item.like_status === 1"
                    @click="unlikepost(item.post_id)"
                    class="ml-n3"
                    icon
                  >
                    <v-icon color="red">mdi-heart</v-icon>
                  </v-btn>
                  <span>{{ item.count_like }} like {{ item.likestatus }}</span>
                  <v-btn icon>
                    <v-icon>mdi-message-outline</v-icon>
                  </v-btn>
                  <span>{{ item.count_comment }} Comments </span>
                  <read-more
                    class="readmore"
                    more-str="read more"
                    :text="item.caption"
                    link="javascript: void(0)"
                    less-str="read less"
                    :max-chars="50"
                  ></read-more>
                  <div class="mt-1 grey--text txtsmall">
                    <v-icon small class="collocateIcon"
                      >mdi-clock-outline</v-icon
                    >
                    <span class="ml-1">{{ item.create_date }}</span>
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </template>
      </v-data-iterator>
      <Addstory ref="Addstory" v-on:edit="editpost" />
      <Timelinedialog ref="Timelinedialog" />
      <Deletepopup ref="Deletepopup" v-on:delete="deletepost"></Deletepopup>
    </v-container>
    <Menu />
  </div>
</template>

<script>
import axios from 'axios'
import Addstory from '@/components/Home/Mystory/Addstory'
import Timelinedialog from '@/components/Timeline/Timelinedialog'
import FriendList from '@/components/Timeline/FriendList'
import Menu from '@/components/Menu'
import Deletepopup from '@/components/DeletePopup'

export default {
  name: 'Timeline',
  components: {
    Addstory,
    FriendList,
    Menu,
    Timelinedialog,
    Deletepopup
  },
  data () {
    return {
      // pathS3: process.env.VUE_APP_S4,
      userprofile: null,
      postData: [],
      post_id: null,
      picture: '',
      caption: '',
      create_date: '',
      edited_date: '',
      nickname: '',
      idpost: '',
      page: 1,
      itemsPerPage: 99999,
      likestatus: null,
      userid: '',
      nopost: false,
      menu: [
        {
          title: 'Edit Post',
          todo: 'edit'
        },
        {
          title: 'Delete Post',
          todo: 'delete'
        }
      ]
    }
  },
  mounted () {
    this.userprofile = JSON.parse(this.$ls.get('userprofile'))[0]
    this.getpost()
    this.idpost = this.info.post_id
  },
  updated: function () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    async likepost (postID) {
      await this.axios.post(process.env.VUE_APP_API + 'like_post', {
        user_id: this.userid,
        post_id: postID
      })
      this.getpost()
      // this.getFollow()
    },
    async unlikepost (postID) {
      await this.axios.post(process.env.VUE_APP_API + 'unlike_post', {
        user_id: this.userid,
        post_id: postID
      })
      this.getpost()
    },
    async getpost () {
      this.userid = JSON.parse(this.$ls.get('userprofile'))[0].user_id
      // this.num = this.$store.state.numberid
      const body = {
        user_id: this.userprofile.user_id,
        friend_id: this.userprofile.user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )
      console.log(result)

      if (result.data.list_post.length === 0) {
        console.log('nopost')
        this.nopost = true
      } else {
        this.nopost = false
        this.likestatus = result.data.like_status
        this.postData = result.data.list_post
        result.data.list_post.forEach(post => {
          var result = post.create_date.split('T')
          var result2 = result[1].split('.')
          post.DAY = result[0]
          post.TIME = result2[0]
        })
      }
    },
    openAddstory (info, todo) {
      this.$refs.Addstory.openDialog(info, todo)
    },

    openTimelinedialog (info) {
      this.$refs.Timelinedialog.openDialog(info)
    },
    openDeletestory (info) {
      this.$refs.Deletepopup.openSheet(info, 'deletepost')
    },
    updateEvents (v) {
      this.getpost(v)
    },
    deletepost () {
      this.dialog = false
      this.$emit('updatepost')
    },
    async editpost (event) {
      const body = {
        user_id: event.user_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_post_by_userid',
        body
      )

      result.data.list_post.forEach(post => {
        if (post.post_id === event.post_id) {
          this.info = post
        }
      })
    }
  },
  computed: {
    updateTimeline () {
      return this.$store.state.updateTimeline
    },
    updatethisPost () {
      return this.$store.state.updatePost
    }
  },
  watch: {
    updateTimeline (newValue, oldValue) {
      if (newValue === true) {
        this.getpost()
        this.$store.commit('getupdateTimelineFALSE')
      }
    },
    async updatethisPost (newValue) {
      if (newValue === true) {
        await this.getpost()
        this.$store.commit('getupdatePost', false)
      }
    }
  }
}
</script>

<style scope>
a:link {
  text-decoration: none !important;
}
a:visited {
  color: #5cbe66 !important;
}
#readmore {
  color: #5cbe66 !important;
}
.readmore p {
  margin-bottom: 0px !important;
}
</style>
