<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary" elevation="0">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Find Home</v-toolbar-title>
        </v-toolbar>
        <v-list three-line subheader>
          <v-container class=" mt-n3">
            <v-row>
              <v-col align="center" cols="12">
                <v-card elevation="0">
                  <v-img
                    width="220"
                    height="220"
                    v-if="info.picture !== ''"
                    :src="info.picture"
                  ></v-img>
                  <v-img
                    width="220"
                    height="220"
                    v-if="info.picture === ''"
                    src="../../assets/icon/icon_PETDY.png"
                  ></v-img>
                </v-card>
              </v-col>
              <v-col align="end" cols="12"
                ><v-icon small class=" greyB--text txtsmall collocateIcon "
                  >mdi-clock-outline</v-icon
                >
                <span class="ml-1  greyB--text txtsmall"
                  >{{ info.create_date }}
                </span>
              </v-col>

              <!-- <v-col align="center" cols="12"><v-icon  class="primary--text collocateIcon">mdi-clock-outline</v-icon> <span class="ml-1 primary--text ">{{info.DAY}}</span> </v-col>
      <v-col align="center" cols="12"><v-icon  class="primary--text  collocateIcon">mdi-map-marker</v-icon><span class="primary--text ">{{ info.place }}</span> </v-col> -->
              <!-- <v-col cols="12"><div class="txtbold">Detail :</div></v-col> -->
              <v-container grid-list-xs class="pa-5">
                <!-- <v-row>
              <v-col align="center" cols="12"><v-icon class="primary--text  collocateIcon">mdi-comment-text-outline</v-icon><span v-html="info.detail"></span> </v-col>

        </v-row> -->
                <v-card height="100%" elevation="0" class="mb-5">
                  <v-row>
                    <v-col cols="2">
                      <v-row justify="center" align-content="center">
                        <v-icon dense color="primary" class=" collocateIcon"
                          >mdi-comment-text-outline</v-icon
                        >
                      </v-row>
                    </v-col>
                    <v-col>
                      <span v-html="info.detail"></span>
                    </v-col>
                  </v-row>

                  <v-row>
                    <v-col cols="2">
                      <v-row justify="center" align-content="center">
                        <v-img
                          src="../../assets/icon/Phone.png"
                          max-height="30"
                          max-width="20"
                        >
                        </v-img>
                      </v-row>
                    </v-col>
                    <v-col>
                      {{ info.contact }}
                    </v-col>
                  </v-row>

                  <v-row>
                    <v-col cols="2">
                      <v-row justify="center" align-content="center">
                        <v-img
                          src="../../assets/icon/Lacation.png"
                          max-height="30"
                          max-width="20"
                        >
                        </v-img>
                      </v-row>
                    </v-col>
                    <v-col>
                      {{ info.place }}
                    </v-col>
                  </v-row>
                </v-card>

                <v-row>
                  <v-col class="bottomBTN">
                    <v-btn
                      block
                      color="hospital"
                      elevation="0"
                      class="txtbold white--text"
                      @click="testOpenchat"
                    >
                      Chat
                    </v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-row>
          </v-container>
        </v-list>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      pathS3: process.env.VUE_APP_S3,
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      info: ''
    }
  },
  methods: {
    testOpenchat () {
      window.open(
        'https://chat.one.th/qrcode/friend?id=VWE3NzA2OGRmOGRmMjUyNGNhODk3MGNkMGJmYjIwMGVj'
      )
    },
    async openchat () {
      const body = {
        one_id: this.info.oneid
      }

      const headers = {
        Authorization:
          'Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1'
      }
      const result = await axios.post(
        'https://chat-api.one.th/manage/get_link_addfriend',
        body,
        { headers }
      )
      window.open(result.data.link)
    },
    openDialog (info) {
      this.$store.commit('getloading', true)
      this.info = info
      this.dialog = true
    },
    closeDialog () {
      this.dialog = false
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  }
}
</script>

<style>
.bottomBTN {
  position: absolute;
  bottom: 0;
  right: 0;
}
</style>
