      <v-app-bar-nav-icon color="white" @click="drawer = !drawer"></v-app-bar-nav-icon>
    </v-app-bar>

    <v-navigation-drawer v-model="drawer" fixed temporary right >
      <MainMenu />
      <MenuSearch ref="MenuSearch"/>
    </v-navigation-drawer>

    <v-main class="pa-0">
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>

import MainMenu from '@/components/MainMenu'
import MenuSearch from '@/components/Searchuser/MenuSearch'

export default {
  components: {
    MainMenu,
    MenuSearch
  },
  data () {
    return {
      drawer: null
    }
  },
  methods: {
    openMenuSearch () {
      this.$refs.MenuSearch.openDialog()
    }
  }

}
</script>
