<template>
    <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar
          dark
          color="primary"
          elevation="0"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>Create Story</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <UploadeImage ref="addpetDialog" v-bind:width="400" v-bind:height="400" v-bind:classupload="true"></UploadeImage>
        <v-row justify="center" align-content="center" class="mx-0">
          <v-col cols="11" >
            <v-row>
              <v-textarea
                label="Post"
                auto-grow
                outlined
                rows="4"
                row-height="30"
                v-model="postCaption"
              ></v-textarea>
            </v-row>

            <v-row>
              <v-btn v-if="todo === 'createpost'"  elevation="0" block color="primary" @click="createStory">POST</v-btn>
              <v-btn v-if="todo === 'editpost'"  elevation="0" block color="primary" @click="editStory">SAVE</v-btn>
            </v-row>
          </v-col>
        </v-row>
    </v-card>

    <v-dialog
      v-model="notiCard"
      transition="dialog-bottom-transition"
    >
      <v-card color="white" class="text-center">
        <v-row>
          <v-col class="pb-0">
            <div class="txtreg" style="font-size:20px">
              Please add story picture
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="py-0">
            <!-- <v-icon x-large color="primary">mdi-check-circle-outline</v-icon> -->
            <sweetalert-icon icon="error"></sweetalert-icon>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>

    <loading/>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from 'axios'
import UploadeImage from '@/components/uploadeImage'
import loading from '@/components/Loading'
import SweetalertIcon from 'vue-sweetalert-icons'
export default {
  components: {
    UploadeImage,
    loading,
    SweetalertIcon
  },
  data () {
    return {
      postCaption: null,
      dialog: false,
      todo: null,
      postID: null,
      formData: null,
      info: '',
      finalIMG: null,
      notiCard: false
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    openNotiCard () {
      this.notiCard = true
      setTimeout(() => {
        this.notiCard = false
      }, 1500)
    },
    openDialog (info, todo) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
      this.postID = info.post_id
      if (todo === 'editpost') {
        console.log('editpost')
        this.todo = todo
        this.$store.commit('getshowImgstatusTRUE')
        this.$store.commit('getImgUrl', info.picture)
        this.postCaption = info.caption
      } else if (todo === 'createpost') {
        this.$store.commit('getshowImgstatusFALSE')
        console.log('createpost')
        this.todo = todo
        this.postCaption = ''
      }
    },
    async createStory () {
      var formData = new FormData()
      if (this.$store.state.imgBlob === null) {
        // this.openNotiCard()
        this.$store.commit('getnoimgSelected', true)
        // console.log(this.notiCard)
      } else {
        this.$store.commit('getloading', true)
        formData.append('file', this.$store.state.imgBlob, 'png')
        formData.append('caption', this.postCaption)
        formData.append('user_create_id', this.$store.state.userID)
        const result = await axios.post(
          process.env.VUE_APP_API + 'add_post',
          formData
        )
        console.log(result)
        this.$store.commit('getcropparemove', true)
        this.$store.commit('getupdatePost', true)
        this.$store.commit('getimgBlob', null)
        this.$store.commit('getImgUrl', null)
        this.$store.commit('getupdateprofile', true)
        this.$store.commit('getupdateTimeline')
        this.$store.commit('getupdatePost', true)

        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.dialog = false
      }
    },
    async editStory () {
      this.$store.commit('getloading', true)
      var formData = new FormData()
      console.log(this.$store.state.imgBlob)
      if (this.$store.state.imgBlob === null) {
        formData.append('file', '')
        formData.append('caption', this.postCaption)
        formData.append('user_create_id', this.$store.state.userID)
        formData.append('post_id', this.postID)
      } else {
        formData.append('file', this.$store.state.imgBlob, 'png')
        formData.append('caption', this.postCaption)
        formData.append('user_create_id', this.$store.state.userID)
        formData.append('post_id', this.postID)
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'edit_post',
        formData
      )
      console.log(result)
      this.$store.commit('getcropparemove', true)
      this.$store.commit('getupdatePost', true)
      this.$store.commit('getimgBlob', null)
      this.$store.commit('getImgUrl', null)
      this.$store.commit('getupdateprofile', true)
      this.$store.commit('getupdateTimeline')
      this.$nextTick(function () {
        if (this.$store.state.loading === true) {
          this.$store.commit('getloading', false)
        }
      })
      this.dialog = false
      this.$emit('edit', this.info)
    }
  }
}
</script>

<style lang="scss" scoped>
.uploadcard{border-style: dashed;}
</style>
