<template>
  <div>
    <Profile />
    <v-container class="pa-0">
      <v-tabs fixed-tabs color="secondary" height="40px" v-model="tab" centered>
        <v-tab>
          <span class="txtsmall txtbold">Pet Location</span>
        </v-tab>
        <v-tab>
          <span class="txtsmall txtbold">My Pet</span>
        </v-tab>
        <v-tab>
          <span class="txtsmall txtbold">My Story</span>
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab" touchless hide-slider="true">
        <v-tab-item> <PetLocation /> </v-tab-item>
        <v-tab-item> <MyPet /> </v-tab-item>
        <v-tab-item> <MyStory /> </v-tab-item>
        <v-overlay z-index="0" opacity=".7" :value="fab"></v-overlay>
        <v-speed-dial
          v-model="fab"
          :bottom="true"
          :right="true"
          :open-on-hover="false"
          direction="top"
          transition="slide-y-reverse-transition"
          class="mb-15"
          fixed
        >
          <template v-slot:activator>
            <v-btn v-model="fab" color="primary" dark fab>
              <v-icon v-if="fab">mdi-close</v-icon>
              <v-icon v-else>mdi-plus</v-icon>
            </v-btn>
          </template>
          <div class="d-flex text-right">
            <span
              class="txtsmall white--text mt-4 mr-1"
              style="white-space: nowrap"
              >Create Pet</span
            >
            <v-btn
              fab
              small
              color="secondary"
              @click="openAddpetFormDialog('', 'createpet')"
              class="ml-4"
            >
              <v-icon>mdi-paw</v-icon>
            </v-btn>
          </div>
          <div class="d-flex text-right mr-2">
            <span
              class="txtsmall white--text mt-4 mr-2"
              style="white-space: nowrap"
              >Create Story</span
            >
            <v-btn
              fab
              small
              color="secondary"
              @click="openAddstory('', 'createpost')"
              class="ml-2"
            >
              <v-icon>mdi-image-outline</v-icon>
            </v-btn>
          </div>
          <div class="d-flex text-right">
            <span
              class="txtsmall white--text mt-4 mr-2"
              style="white-space: nowrap"
              >QR scanner</span
            >
            <v-btn fab small color="secondary" @click="openscanner()">
              <v-icon>mdi-qrcode-scan</v-icon>
            </v-btn>
          </div>
        </v-speed-dial>
      </v-tabs-items>
    </v-container>
    <AddpetForm ref="AddpetFormDialog" />
    <Addstory ref="Addstory" />
    <qrdialog ref="qr_dialog"></qrdialog>
    <Menu />
  </div>
</template>

<script>
import Menu from '@/components/Menu'
import Profile from '@/components/Home/Profile'
import PetLocation from '@/components/Home/PetLocation'
import MyPet from '@/components/Home/Mypet/MyPet'
import MyStory from '@/components/Home/Mystory/MyStory'
import AddpetForm from '@/components/Home/Mypet/Addpet/AddpetForm'
import Addstory from '@/components/Home/Mystory/Addstory'
import axios from 'axios'
import qrdialog from '@/components/Home/QRscanner_dialog'
export default {
  name: 'Home',
  components: {
    MyStory,
    MyPet,
    PetLocation,
    Profile,
    Menu,
    AddpetForm,
    Addstory,
    qrdialog
  },

  created () {
    localStorage.setItem('active', 0)
    console.log(this.$store.state.userID)
  },
  data () {
    return {
      tab: null,
      fab: false,
      qrscan: false
    }
  },
  updated: function () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    openscanner () {
      this.$refs.qr_dialog.openDialog()
    },
    openAddpetFormDialog (info, todo) {
      this.$refs.AddpetFormDialog.openDialog(info, todo)
    },
    openAddstory (info, todo) {
      this.$refs.Addstory.openDialog(info, todo)
    }
  },
  watch: {
    tab (newValue, oldValue) {
      if (oldValue === 0 && newValue !== 0) {
        this.$store.commit('getclosemarkerInfo', true)
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    var paramsString = window.location.href
    var onetoken = []
    var searchParams = new URLSearchParams(paramsString)
    searchParams.forEach(token => {
      onetoken.push(token)
      if (onetoken[0] !== '') {
        // localStorage.clear()
        localStorage.setItem('token', onetoken[0])
      }
    })

    const body = {
      source: localStorage.getItem('token')
    }
    const headers = {
      Authorization:
        'Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1'
    }

    const result = axios.post(
      'https://chat-api.one.th/manage/api/v1/login_onechat_token',
      body,
      { headers }
    )
    result.then(data => {
      localStorage.setItem(
        'oneprofile',
        JSON.stringify(data.data.account.profile)
      )
      const body2 = {
        one_id: data.data.account.profile.one_id
      }
      const result2 = axios.post(
        process.env.VUE_APP_API + 'get_user_by_oneid',
        body2
      )

      result2.then(data2 => {
        if (data2.data.message === 'user not found') {
          next({ name: 'Register' })
        } else {
          // if (data2.data.message === undefined) { next({ name: 'Register' }) } else {
          localStorage.setItem(
            'userprofile',
            JSON.stringify(data2.data.user_data)
          )
          next()
        }
      })
    })
    this.$store.commit(
      'getuserID',
      JSON.parse(localStorage.getItem('userprofile'))[0].user_id
    )
  }
}
</script>

<style scoped>
>>> .v-speed-dial--direction-top .v-speed-dial__list,
.v-speed-dial--direction-bottom .v-speed-dial__list {
  left: -35px !important;
}
</style>

