<template>
  <div>
    <v-card tile elevation="0" class="mt-1" color="#333333" height="660">
      <v-container class="pt-16">
        <v-row justify="center" class="mt-16">
          <v-col>
            <v-row justify="center">
              <qrcode-stream
                id="scanner"
                class="my-5 pa-2"
                @decode="onDecode"
                @init="onInit"
              ></qrcode-stream>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </v-card>

    <v-bottom-sheet
      overlay-opacity="0.7"
      v-model="sheet"
      v-if="permission === true"
    >
      <v-row>
        <v-col class="pb-16 pl-7">
          <v-row justify="center" align-content="center">
            <v-card elevation="0" width="325" class="text-center">
              <p
                class="txtconferm txtreg px-4 pt-5"
                v-if="permission_detail === 1"
              >
                Permission denied. Please disconnect the device from another
                user account.
              </p>

              <p
                class="txtconferm txtreg px-4 pt-5"
                v-if="permission_detail === 2"
              >
                Permission denied. This collar is not pair with this pet .
              </p>

              <p
                class="txtconferm txtreg px-4 pt-5"
                v-if="permission_detail === 3"
              >
                This collar is unpair
              </p>

              <v-divider></v-divider>
              <v-row>
                <v-col cols="12" @click="closesheet()">
                  OK
                </v-col>
              </v-row>
            </v-card>
          </v-row>
        </v-col>
      </v-row>
    </v-bottom-sheet>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  props: ['action', 'pairstatus', 'ScanAction', 'searchUser'],
  data () {
    return {
      sheet: true,
      dialog: false,
      birthdate: '',
      resultQR: '',
      HWstatus: '',
      HWserial: '',
      permission: false,
      permission_detail: null,
      minor: null
    }
  },
  methods: {
    async onInit (promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = 'ERROR: you need to grant camera access permisson'
        } else if (error.name === 'NotFoundError') {
          this.error = 'ERROR: no camera on this device'
        } else if (error.name === 'NotSupportedError') {
          this.error = 'ERROR: secure context required (HTTPS, localhost)'
        } else if (error.name === 'NotReadableError') {
          this.error = 'ERROR: is the camera already in use?'
        } else if (error.name === 'OverconstrainedError') {
          this.error = 'ERROR: installed cameras are not suitable'
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = 'ERROR: Stream API is not supported in this browser'
        }
      }
    },
    closesheet () {
      this.sheet = false
      this.$emit('close')
    },
    async onDecode (decodedString) {
      var pathname = new URL(decodedString).pathname
      this.resultQR = pathname.split('pet_profile/')[1]
      if (this.searchUser === true) {
        this.$emit('QRcode', this.resultQR)
        this.searchUser = false
      } else {
        await this.CheckHWstatus()
        if (
          this.HWstatus === null ||
          this.HWstatus === 'activate' ||
          this.HWstatus === '' ||
          this.HWstatus === 'non_active'
        ) {
          this.$emit('checkpair', this.HWserial)
          this.$emit('device_minor', this.minor)
          if (this.action === 'disconnect') {
            if (this.$store.state.Pair_status === false) {
              this.permission = true
              this.permission_detail = 2
              this.$store.commit('getPair_status', null)
            }
          } else if (this.action === 'connect') {
            this.$emit('QRcode', this.resultQR)
          }
        } else if (this.HWstatus === 'activate_pair') {
          if (this.ScanAction === 'connectpet') {
            if (this.action === 'disconnect') {
              this.$emit('checkpair', this.HWserial)
              if (this.$store.state.Pair_status === false) {
                this.permission = true
                this.permission_detail = 2
                this.$store.commit('getPair_status', null)
              }

              if (this.$store.state.Pair_status === true) {
                this.$emit('QRcode', this.resultQR)
                this.$emit('unpair', true)
                this.$store.commit('getPair_status', null)
              }
            } else if (this.action === 'connect') {
              this.permission = true
              this.permission_detail = 1
            }
          } else if (this.ScanAction === 'petprofile') {
            this.$router.push({ name: 'PetprofilefromQR', params: { token: this.resultQR } })
            // window.open(decodedString)
            this.$emit('closeDialog')
          }
        }
      }
    },
    async CheckHWstatus () {
      const body = {
        hw_qrcode: this.resultQR
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'show_Profile',
        body
      )
      this.HWstatus = result.data.data[0].hardware_status
      this.HWserial = result.data.data[0].hardware_sn
      this.minor = result.data.data[0].minor_id
    }
  }
}
</script>

<style scoped>
#scanner {
  height: 300px;
  width: 300px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='white' stroke-width='16' stroke-dasharray='150' stroke-dashoffset='75' stroke-linecap='square'/%3e%3c/svg%3e");
}
.txtconferm {
  font-size: 15px;
}
</style>
