<template>
  <!-- -------- -->
  <v-container class=" mt-n3">
    <v-row cols="12">
      <v-col cols="12">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="title"
            :rules="requireRule | TopicRules"
            outlined
            label="Topic"
            clearable
            required
          ></v-text-field>
          <v-text-field
            v-model="matchingplace"
            :rules="PlaceRules"
            outlined
            label="Place"
            clearable
          ></v-text-field>
          <v-text-field
            v-model="contact"
            :rules="MobileRules"
            outlined
            label="Mobile Number"
            counter="10"
            maxlength="10"
            clearable
          ></v-text-field>
        </v-form>
      </v-col>
      <getCoordinate ref="getCoordinate" v-bind:info="lng"/>
    </v-row>
    <v-col>
      <v-btn
        elevation="0"
        block
        class="primary white--text mt-10"
        :disabled="!valid"
        color="primary"
        v-if="this.status === 'create'"
        @click="createMatching()"
      >
        Save
      </v-btn>
      <v-btn
        elevation="0"
        block
        class="primary white--text mt-10"
        :disabled="!valid"
        color="primary"
        v-if="this.status === 'edit'"
        @click="edit()"
      >
        Save
      </v-btn>
    </v-col>
    <loading />
  </v-container>
</template>

<script>
import getCoordinate from '@/components/getCoordinate'
import axios from 'axios'
import loading from '@/components/Loading'
export default {
  props: ['status', 'info'],
  data () {
    return {
      pathS3: process.env.VUE_APP_S3,
      valid: true,
      title: '',
      requireRule: [v => !!v || 'This field is required'],
      TopicRules: [v => !!v || 'Topic is required'],
      matchingplace: '',
      PlaceRules: [v => !!v || 'Placel is required'],
      contact: '',
      MobileRules: [
        v => !!v || 'Mobile Number is required',
        v => (v && v.length === 10) || 'The number of digits must be 10',
        v => /^[0-9]*$/.test(v) || 'this field must be numeric'
      ],

      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      type: '',
      data: {
        title: null,
        matchingplace: null,
        contact: null,
        longitude: '',
        latitude: '',
        pet_id: '',
        userupdate_id: ''
      },
      lat: null,
      lng: null
    }
  },
  async created () {
    if (this.status === 'edit') {
      const body = {
        pet_id: this.info.pet_id
      }
      const result = await axios.post(
        process.env.VUE_APP_API + 'get_pet_match_by_id',
        body
      )
      console.log('result', result)

      this.title = result.data.pet_match[0].title
      this.matchingplace = result.data.pet_match[0].matchingplace
      this.contact = result.data.pet_match[0].contact
      this.lat = result.data.pet_match[0].latitude
      this.lng = result.data.pet_match[0].longitude
      this.$store.commit('setMatchLatLng', { lat: this.lat, lng: this.lng })
      this.$store.commit('getupdatematchmarker', true)
    }
  },
  components: {
    getCoordinate,
    loading
  },
  computed: {
    resetForm () {
      return this.$store.state.resetForm
    }
  },
  watch: {
    status (newval) {
      this.$nextTick(async () => {
        if (newval === 'edit') {
          const body = {
            pet_id: this.info.id
          }
          const result = await axios.post(
            process.env.VUE_APP_API + 'get_pet_match_by_id',
            body
          )
          console.log('result', result)

          this.title = result.data.pet_match[0].title
          this.matchingplace = result.data.pet_match[0].matchingplace
          this.contact = result.data.pet_match[0].contact
          this.lat = result.data.pet_match[0].latitude
          this.lng = result.data.pet_match[0].longitude
          this.$store.commit('setMatchLatLng', { lat: this.lat, lng: this.lng })
          this.$store.commit('getupdatematchmarker', true)
        } else if (newval === 'create') {
          this.data.title = null
          this.data.matchingplace = null
          this.data.contact = null
          this.data.pet_id = this.info.pet_id
          this.data.userupdate_id = null
        }
      })
    },
    resetForm (newval) {
      if (newval === true) {
        this.$refs.form.reset()
        this.$store.commit('getresetForm', false)
      }
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },

  methods: {
    createMatching () {
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        this.data.title = this.title
        this.data.matchingplace = this.matchingplace
        this.data.contact = this.contact
        this.data.longitude = this.$store.state.Locationlng
        this.data.latitude = this.$store.state.Locationlat
        this.data.pet_id = this.info.pet_id
        this.data.userupdate_id = this.info.user_id
        this.axios
          .post(process.env.VUE_APP_API + 'add_pet_match', this.data)
          .then(res => {
            console.log('result', res)
          })
        this.$nextTick(function () {
          if (this.$store.state.loading === true) {
            this.$store.commit('getloading', false)
          }
        })
        this.$emit('todo', 'createMatching')
        this.$store.commit('getupdateMatching')
      }
    },
    edit () {
      if (this.$refs.form.validate() === true) {
        this.$store.commit('getloading', true)
        this.data.title = this.title
        this.data.matchingplace = this.matchingplace
        this.data.contact = this.contact
        this.data.longitude = this.$store.state.Locationlng
        this.data.latitude = this.$store.state.Locationlat
        this.data.pet_id = this.info.pet_id
        this.data.userupdate_id = this.info.user_id
        this.axios
          .post(process.env.VUE_APP_API + 'edit_pet_match', this.data)
          .then(res => {
            console.log('result', res)
          })
      }
      this.$nextTick(function () {
        if (this.$store.state.loading === true) {
          this.$store.commit('getloading', false)
        }
      })
      this.$emit('todo', 'editMatching')
      this.$store.commit('getupdateMatching')
    },
    async openDialog (info) {
      this.$store.commit('getloading', true)
      this.info = info
      this.dialog = true
      this.$refs.form.resetValidation()
      if (this.status === 'Edit') {
        this.$store.commit('getshowImgstatusTRUE')
        const body = {
          pet_id: this.info.id
        }
        const result = await axios.post(
          process.env.VUE_APP_API + 'get_pet_match_by_id',
          body
        )
        console.log('result', result)
        this.data.longitude = result.data.pet_match[0].longitude
        this.data.latitude = result.data.pet_match[0].latitude
        this.data.title = result.data.pet_match[0].title
        this.data.matchingplace = result.data.pet_match[0].matchingplace
        this.data.contact = result.data.pet_match[0].contact
        this.data.pet_id = result.data.pet_match[0].pet_id
        this.data.userupdate_id = result.data.pet_match[0].userupdate_id
      } else if (this.status === 'Create') {
        this.$store.commit('getshowImgstatusTRUE')
        this.data.title = null
        this.data.matchingplace = null
        this.data.contact = null
        this.data.pet_id = info.pet_id
        this.data.userupdate_id = null
      }
    },
    closeDialog () {
      this.$refs.form.reset()
      this.dialog = false
    },
    validate () {
      this.$refs.form.validate()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    }
  }
  // computed: mapState(['resetForm'])
}
</script>
<style scoped></style>
