<template>
    <v-dialog
      v-model="dialog"
      width="800"
      height="100"
      persistent
    >
      <v-card  width="800">
         <v-toolbar
          class="toolbar1"
          dark
          color="primary"
        >
          <v-toolbar-title>Edit Display</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <v-card tile elevation="0">
             <v-col
          cols="12"
          sm="6"
          md="4"
        >
          <v-textarea
            label="display"
            auto-grow
            outlined
            rows="1"
            row-height="125"
            dense
            v-model="petDetail"
          ></v-textarea>
        </v-col>
        <v-divider  class="mt-n5"></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeDialog">CLOSE</v-btn>
          <v-btn text color="primary" @click="editDisplay">SAVE</v-btn>
        </v-card-actions>
        </v-card>
      </v-card>
      <loading/>
    </v-dialog>
</template>

<script>
import axios from 'axios'
import loading from '@/components/Loading'
export default {
  components: {
    loading
  },
  data () {
    return {
      dialog: false,
      display: '',
      info: '',
      petDetail: ''
    }
  },
  watch: {
    dialog (newValue) {
      if (newValue === false) {
        this.petDetail = ''
      }
    }
  },
  updated () {
    this.$nextTick(function () {
      if (this.$store.state.loading === true) {
        this.$store.commit('getloading', false)
      }
    })
  },
  methods: {
    closeDialog () {
      this.dialog = false
    },
    openDialog (info, todo) {
      this.$store.commit('getloading', true)
      this.dialog = true
      this.info = info
      if (todo === 'editdisplaytext') {
        this.petDetail = this.info.pet_detail
      }
    },
    async editDisplay () {
      this.$store.commit('getloading', true)
      var formData = new FormData()
      formData.append('pet_id', this.info.pet_id)
      formData.append('file', '')
      formData.append('pet_type', this.info.pettype)
      formData.append('pet_name', this.info.name)
      formData.append('breed', this.info.breed)
      formData.append('gender', this.info.gender)
      formData.append('furcolor', this.info.furcolor)
      formData.append('weight', this.info.weight)
      formData.append('birthdate', this.info.birthdate)
      formData.append('pet_detail', this.petDetail)

      const result = await axios.post(
        process.env.VUE_APP_API + 'v3/edit_pet_info',
        formData
      )
      console.log('result : ', result)
      this.$emit('updatedisplay', this.info)
      this.$store.commit('getupdateMypet', true)
      this.$nextTick(function () {
        if (this.$store.state.loading === true) {
          this.$store.commit('getloading', false)
        }
      })
      this.dialog = false
    }
  }
}
</script>

